

















import {GlobalTag} from "@/types/ApiTypes";
import {Component, Vue} from "vue-property-decorator";

@Component
export default class GlobalTagsView extends Vue {
  tagList: GlobalTag[] = [];

  mounted(): void {
    this.getData();
  }

  getData(): void {
    this.api.getTagList(null).then((tagList) => {
      this.tagList = tagList;
    });
  }

  addTag(): void {
    this.$prompt("Tag Name", "New Tag", {
      confirmButtonText: "Submit",
      cancelButtonText: "Cancel",
    })
      .then((action: any) => {
        if (this.tagList.some((tag) => tag.name === action.value)) {
          this.$message({type: "info", message: "Tag has been created."});
        } else {
          this.api.addTag({name: action.value}).then(() => {
            this.getData();
          });
        }
      })
      .catch(() => {
        this.$message({type: "info", message: "Cancel!"});
      });
  }

  deleteTag(id: number): void {
    this.$confirm("Do you want to delete this Tag?", "Delete", {})
      .then(() => {
        this.api.deleteTag(id).then(() => {
          this.getData();
          this.$message({
            type: "success",
            message: "success!",
          });
        });
      })
      .catch(() => {
        this.$message({
          type: "info",
          message: "cancel",
        });
      });
  }
}
