import {Component} from "vue";

export interface Tab {
  name: string;
  tab: Component;
}

export enum TABS_MUTATION {
  UPDATE_CURRENT_TAB = "UPDATE_CURRENT_TAB",
}

export enum TABS_ACTION {
  UPDATE_CURRENT_TAB = "tabs/UPDATE_CURRENT_TAB",
}

export interface Tabs {
  all: Tab[];
  currentTab: Tab | null;
}

export interface TabsMutations {
  UPDATE_CURRENT_TAB(tab: Tab): void;
}

export interface TabsActions {
  UPDATE_CURRENT_TAB(tabName: string): void;
}
