





















































































































import AddSequenceModal from "@/components/sequence/AddSequenceModal.vue";
import EditSequenceModal from "@/components/sequence/EditSequenceModal.vue";
import ImportSequenceModal from "@/components/sequence/ImportSequenceModal.vue";
import {formatTimeRange} from "@/core/utils/formatTimeRange";
import {paramsToQuery} from "@/core/utils/paramsToQuery";
import {SequenceManager} from "@/types/ApiTypes";
import {Component, Vue} from "vue-property-decorator";
import {Getter} from "vuex-class";
import {SequenceJSON} from "@/types/SequenceJSON";

@Component({
  components: {ImportSequenceModal, EditSequenceModal, AddSequenceModal},
})
export default class SequenceManagerView extends Vue {
  @Getter("permission/id") readonly id!: number;

  @Getter("permission/status") readonly status!: number;

  displayAddModal: boolean = false;
  displayImportModal: boolean = false;
  activeTab = "Customize";
  searchDate = null;
  data: SequenceManager[] = [];
  originalData: SequenceManager[] = [];
  displayEditModal: boolean = false;
  editId: number = null;
  duplicateData: SequenceManager = null;
  global: boolean = false;
  searchString: string = "";

  editSequence(id: number): void {
    this.editId = id;
    this.displayEditModal = true;
  }

  sortDate(a, b) {
    return Date.parse(a.created_time) > Date.parse(b.created_time);
  }

  removeSequence(id: number): void {
    this.$confirm("Do you want to delete this Sequence?", "Delete", {})
      .then(() => {
        this.api.deleteSm(id).then(() => {
          this.getData();
          this.$message({
            type: "success",
            message: "success!",
          });
        });
      })
      .catch(() => {
        this.$message({
          type: "info",
          message: "cancel",
        });
      });
  }

  closeEdit(): void {
    this.displayEditModal = false;
    this.editId = null;
  }

  mounted(): void {
    const tab = this.status === 1 ? "Global" : "Customize";
    this.activeTab = tab;
    this.changeTab({label: tab});
  }

  changeTab(tab): void {
    this.global = tab.label !== "Customize";
    this.getData();
  }

  getData(): void {
    this.duplicateData = null;
    let params;
    if (!this._.isEmpty(this.searchDate)) {
      const searchDate = formatTimeRange(this.searchDate);
      params = {gte: searchDate[0], lte: searchDate[1]};
    }
    params = this.global ? {...params, is_customize: "False"} : {...params, user: this.id, is_customize: "True"};
    this.api.getSmList({query: paramsToQuery(params), sort_by: "created_time:1"}).then((res) => {
      this.originalData = res;
      this.data = this.originalData;
    });
  }

  statusLabel(status: boolean): string {
    return status ? "Enable" : "Disable";
  }

  computeTaskNum(seq: SequenceManager): string {
    const content: SequenceJSON = seq.content ? JSON.parse(seq.content) : null;
    return content ? String(content.tasks.length) : "0";
  }

  computeEventNum(seq: SequenceManager): string {
    if (seq.content === null) {
      return "0";
    }
    const content: SequenceJSON = JSON.parse(seq.content);
    const eventsNumber =
      content.tasks.length > 0
        ? content.tasks.map((task) => task.events.length).reduce((a: number, b: number) => a + b)
        : 0;
    return String(eventsNumber);
  }

  copyJSON(seq: SequenceManager): void {
    const result = seq.content;
    const txtContent = "data:text/csv;charset=utf-8,\uFEFF" + result;
    const link = document.createElement("a");
    link.href = encodeURI(txtContent);
    link.download = `${seq.sequence_name}_JSON.txt`;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
    this.$copyText(seq.content);
    this.$message({message: "JSON copied", type: "success"});
  }

  duplicateSequence(seq: SequenceManager): void {
    this.duplicateData = seq;
    this.displayAddModal = true;
  }

  cleanDuplicated(): void {
    this.displayAddModal = false;
    this.duplicateData = null;
  }

  enableSequence(seq: SequenceManager): void {
    this.$confirm("Are you sure?", `${this.statusLabel(seq.is_disable)} Sequence`).then(() => {
      this.api.editSm({is_disable: !seq.is_disable}, seq.id).then(() => this.getData());
    });
  }

  inputString(str: string): void {
    if (str === "") {
      this.data = this.originalData;
    } else {
      this.data = this.originalData.filter((it: SequenceManager) => {
        return Object.values(it).some((val) => {
          if (typeof val === "string") {
            return val.toUpperCase().includes(str.toUpperCase());
          }
          return false;
        });
      });
    }
  }
}
