















import AsModal from "@/mixins/AsModal";
import {Component, Emit, Mixins, Prop} from "vue-property-decorator";

@Component
export default class EventPauseModal extends Mixins(AsModal) {
  @Emit("refresh") refresh(): void {}
  @Prop({type: Object, default: () => null}) readonly data;

  mounted() {
    if (this.data) {
      this.eventData = this._.pick(this.data, ["pauseMode", "eventLength", "duration"]);
    }
  }

  eventData = {
    pauseMode: "Countdown",
    eventLength: 0,
    duration: 0,
  };

  eventFields: object[] = [
    {
      title: "Pause Mode",
      type: "select",
      options: [
        {
          title: "Countdown",
        },
        {
          title: "Measurement Stop",
        },
      ],
      placeholder: "",
    },
    {
	//   title: "Event Length [s]",
	  title: "Duration",
      type: "input",
    },
  ];
}
