







































































































































import EditEventModal from "@/components/events/EditEventModal.vue";
import EventModal from "@/components/events/EventModal.vue";
import {initialSm} from "@/core/constants/api/initialSm";
import {GlobalTag, SequenceManager} from "@/types/ApiTypes";
import {Component, Emit, Mixins, Prop, Watch} from "vue-property-decorator";
import {Getter} from "vuex-class";
import AsModal from "../../mixins/AsModal";
import {initialSequenceJSON, initialTask} from "@/core/constants/initialSequenceJSON";
import {SequenceEvent, SequenceJSON, SequenceTask} from "@/types/SequenceJSON";
@Component({
  components: {EditEventModal, EventModal},
})
export default class EditSequenceModal extends Mixins(AsModal) {
  @Getter("permission/status") readonly status!: number;
  @Prop(Number) readonly id!: number;

  @Emit("refresh") refresh(): void {}

  displayEventModal: boolean = false;
  displayEditEventModal: boolean = false;
  selectedTaskId: number = 0;
  newEventId: number = 0;
  formData: SequenceManager = {...initialSm()};
  tags: number[] = [];
  tagName: string = "";
  sequenceJSON: SequenceJSON = initialSequenceJSON();
  tagList: GlobalTag[] = [];
  selectedEvent: SequenceEvent = null;
  measurementsource: any = [];
  analysissource: any = [];

  @Watch("visible")
  onVisibleChange(val: boolean): void {
    if (val) {
      this.api.getTagList(null).then((res) => {
        this.tagList = res;
      });
    }
    this.api.getSm(this.id).then((res) => {
      this.formData = res;
      this.sequenceJSON = res.content ? JSON.parse(res.content) : initialSequenceJSON();
      this.tags = this.formData.tags
        ? this.formData.tags.split(",").map((tag) => {
            return Number(tag);
          })
        : [];
    });
  }

  addTag(): void {
    this.api.addTag({name: this.tagName}).then((res) => {
      this.api.getTagList(null).then((tagList) => {
        this.tagList = tagList;
        this.tags = [...this.tags, res.id];
        this.tagName = null;
      });
    });
  }

  getVersion(): void {
    this.$prompt("Version Name", "Get Version", {
      confirmButtonText: "Submit",
      cancelButtonText: "Cancel",
      inputValue: String(this.formData.version),
    })
      .then((action: any) => {
        this.formData.version = action.value;
        this.submit();
      })
      .catch(() => {
        this.$message({type: "info", message: "Cancel!"});
      });
  }

  submit(): void {
    this.formData.tags = this.tags.join(",");
    this.sequenceJSON.name = this.formData.sequence_name;
    this.formData.content = JSON.stringify(this.sequenceJSON);
    this.formData.user = this.formData.user ? (this.formData.user as any).id : null;

    this.api.editSm(this.formData, this.formData.id).then(() => {
      this.refresh();
      this.clean();
    });
  }

  clean(): void {
    this.formData = {...initialSm()};
    this.sequenceJSON = initialSequenceJSON();
    this.tags = [];
    this.tagName = "";
    this.dismiss();
  }

  addTask(): void {
    this.$prompt("Task Name", "New Task", {
      confirmButtonText: "Add",
      cancelButtonText: "Cancel",
    })
      .then((action: any) => {
        const id = this.sequenceJSON.tasks.length + 1;
        const task = initialTask(action.value, id);
        this.sequenceJSON.tasks.push(task);
      })
      .catch(() => {
        this.$message({
          type: "info",
          message: "Cancel!",
        });
      });
  }

  addEvent(taskId: any): void {
    this.newEventId = this.sequenceJSON.tasks[taskId].events.length + 1;
    this.displayEventModal = true;
    this.selectedTaskId = taskId;
    this.measurementsource = this.sequenceJSON.tasks[taskId].events.reduce((result, item) => {
      if (item.type == "Measurement") {
        result.push(item.name);
      }
      return result;
    }, []);
    this.analysissource = this.sequenceJSON.tasks[taskId].events.reduce((result, item) => {
      if (item.type == "Analysis") {
        result.push(item.name);
      }
      return result;
    }, []);
  }

  getEvent(event: SequenceEvent): void {
    this.sequenceJSON.tasks[this.selectedTaskId].events.push({...event, startTime: "", status: ""});
  }

  patchEvent(event: SequenceEvent): void {
    this.sequenceJSON.tasks[this.selectedTaskId - 1].events[event.id - 1] = event;
    this.selectedEvent = null;
    this.refreshPage(this.selectedTaskId - 1);
  }

  refreshPage(taskId: number): void {
    this.sequenceJSON.tasks[taskId].events.push({
      name: "",
      type: "Pause",
      id: 0,
    });
    this.sequenceJSON.tasks[taskId].events.pop();
  }

  editEvent(event: SequenceEvent, id: number): void {
    let taskId = id - 1;
    this.selectedEvent = event;
    this.displayEditEventModal = true;
    this.selectedTaskId = id;
    this.measurementsource = this.sequenceJSON.tasks[taskId].events.reduce((result, item) => {
      if (item.type == "Measurement") {
        result.push(item.name);
      }
      return result;
    }, []);
    this.analysissource = this.sequenceJSON.tasks[taskId].events.reduce((result, item) => {
      if (item.type == "Analysis") {
        result.push(item.name);
      }
      return result;
    }, []);
  }

  duplicateTask(task: SequenceTask): void {
    const id = this.sequenceJSON.tasks.length + 1;
    const duplicated = {id, ...task};
    this.sequenceJSON.tasks.push(duplicated);
  }

  editTaskName(name: string, index: number): void {
    this.$prompt("Task Name", "Rename", {
      confirmButtonText: "Save",
      cancelButtonText: "Cancel",
      inputValue: name,
    })
      .then((action: any) => {
        this.sequenceJSON.tasks[index].name = action.value;
      })
      .catch(() => {
        this.$message({
          type: "info",
          message: "Cancel!",
        });
      });
  }

  removeTask(index: number): void {
    this.sequenceJSON.tasks.splice(index, 1);
  }

  removeEvent(eventIndex: number, taskId: number): void {
    this.sequenceJSON.tasks[taskId - 1].events.splice(eventIndex, 1);
  }
}
