


























import AsModal from "@/mixins/AsModal";
import {ChangePassword} from "@/types/ApiTypes";
import {Component, Emit, Mixins} from "vue-property-decorator";
import {Getter} from "vuex-class";

@Component
export default class ChangePasswordModal extends Mixins(AsModal) {
  @Getter("permission/id") readonly userId!: number;
  @Emit("refresh") refresh(): void {}

  formData: ChangePassword = {password: "", confirm_password: ""};
  clean(): void {
    this.formData = {password: "", confirm_password: ""};
    this.dismiss();
  }

  change(): void {
    const {password, confirm_password} = this.formData;
    if (password === confirm_password) {
      this.api.patchUserAccount({password}, this.userId).then(() => this.clean());
    } else {
      this.$message({
        type: "error",
        message: "Password can not be different",
      });
    }
  }
}
