
































import {paramsToQuery} from "@/core/utils/paramsToQuery";
import {LabSerializerWithPopulate} from "@/types/ApiTypes";
import {TABS_ACTION, TabsActions} from "@/types/store/TabStore";
import {Component, Vue} from "vue-property-decorator";
import {Action, Getter} from "vuex-class";

@Component
export default class MyLabsView extends Vue {
  @Getter("permission/id") readonly id!: number;
  @Getter("permission/email") readonly email!: string;

  @Action(TABS_ACTION.UPDATE_CURRENT_TAB)
  updateTab!: TabsActions["UPDATE_CURRENT_TAB"];

  goToTDBSetting(lab: LabSerializerWithPopulate): void {
    this.$router.push({
      query: {
        tab: "TransducerDbSettings",
        id: String(lab.id),
        ch: String(lab.device_manager.ch_num),
        sn: String(lab.device_manager.serial_num),
      },
    });
    this.updateTab("TransducerDbSettings");
  }

  goToProject(lab: LabSerializerWithPopulate): void {
    this.$router.push({query: {tab: "ProjectManager", id: String(lab.id)}});
    this.updateTab("ProjectManager");
  }

  data: LabSerializerWithPopulate[] = [];

  mounted(): void {
    this.getData();
  }

  getData(): void {
    this.api.getLabList({query: paramsToQuery({user: this.id})}).then((res) => {
      this.data = res;
    });
  }

  labStatus(lab: LabSerializerWithPopulate): string {
    const {device_manager, status} = lab;
    const deviceOnline = device_manager.online_device;
    return !deviceOnline
      ? "off-line"
      : status > 1
      ? "danger"
      : status > 0
      ? lab.device_manager.certificate === this.email
        ? "normal"
        : "warn"
      : "normal";
  }

  labStatusLabel(lab: LabSerializerWithPopulate): string {
    const {device_manager, status} = lab;
    const deviceOnline = device_manager.online_device;
    return !deviceOnline ? "Offline" : status > 1 ? "Occupied" : status > 0 ? "Viewing" : "Available";
  }
}
