import {SequenceJSON, SequenceTask} from "@/types/SequenceJSON";

export function initialSequenceJSON(id: number = 1): SequenceJSON {
  return {
    id,
    name: "",
    tasks: [],
  };
}

export function initialTask(name: string, id: number = 1): SequenceTask {
  return {
    id,
    name,
    events: [],
    eventName: "",
    status: "",
  };
}
