































import {initialPlanManagement} from "@/core/constants/api/initialPlanManagement";
import AsModal from "@/mixins/AsModal";
import {PlanManagement} from "@/types/ApiTypes";
import {Form} from "element-ui";
import {Component, Emit, Mixins, Watch, Prop} from "vue-property-decorator";

@Component
export default class EditPlanModal extends Mixins(AsModal) {
  @Emit("refresh") refresh(): void {}

  @Prop(Number) readonly id!: number;

  @Watch("visible")
  onVisibleChange(val: boolean): void {
    if (val) {
      this.getEditPlan();
    } else {
      this.formData = {...initialPlanManagement()};
    }
  }

  formData: PlanManagement = {...initialPlanManagement()};

  getEditPlan(): void {
    this.api.getPlanManagement(this.id).then((res) => {
      this.formData = res;
    });
  }

  putPlanManagement(): void {
    (this.$refs.form as Form).validate((valid) => {
      if (valid) {
        const params = {...this._.omit(this.formData, ["has_user"])};
        this.api.putPlanManagement(params, this.id).then(() => {
          this.refresh();
          this.clean();
        });
      } else {
        return false;
      }
    });
  }

  clean(): void {
    this.formData = {...initialPlanManagement()};
    this.dismiss();
  }

  rules = {
    plan_name: [{required: true, message: "Plan Name is required.", trigger: "change"}],
    basic_fee: [{required: true, message: "Basic Fee(NT$) is required.", trigger: "change"}],
    storage: [{required: true, message: "Storage unit price(per GB) is required.", trigger: "change"}],
    network_traffic: [{required: true, message: "Network traffic unit price(per GB) is required.", trigger: "change"}],
    api_price_per_time: [{required: true, message: "API time unit price is required.", trigger: "change"}],
    markup_multiplier: [{required: true, message: "Ｍarkup multiplier is required.", trigger: "change"}],
  };
}
