

























import {initialOTA} from "@/core/constants/api/initialOTA";
import AsModal from "@/mixins/AsModal";
import {OTASerializerWithoutForeignKey} from "@/types/ApiTypes";
import {Form} from "element-ui";
import {Component, Emit, Mixins} from "vue-property-decorator";

@Component
export default class AddOTAModal extends Mixins(AsModal) {
  @Emit("refresh") refresh(): void {}

  formData: OTASerializerWithoutForeignKey = {...initialOTA()};

  add(): void {
    (this.$refs.form as Form).validate((valid) => {
      if (valid) {
        this.api.addOTA(this.formData).then(() => {
          this.refresh();
          this.clean();
        });
      } else {
        return false;
      }
    });
  }

  get isUploading(): boolean {
    return this.formData.download_url === "uploading...";
  }

  upload(options: {file: File}): Promise<void> {
    this.formData.download_url = "uploading...";
    return this.api
      .upload(options.file, "firmware")
      .then((res) => {
        this.formData.download_url = res;
      })
      .catch(console.error);
  }

  clean(): void {
    this.formData = {...initialOTA()};
    this.dismiss();
  }

  rules = {
    version: [{required: true, message: "Version is required.", trigger: "change"}],
    download_url: [{required: true, message: "Upload Firmware is required.", trigger: "change"}],
  };
}
