




















































import AddOTAModal from "@/components/ota/AddOTAModal.vue";
import EditOTAModal from "@/components/ota/EditOTAModal.vue";
import {formatTimeRange} from "@/core/utils/formatTimeRange";
import {paramsToQuery} from "@/core/utils/paramsToQuery";
import {OTA} from "@/types/ApiTypes";
import {Component, Vue} from "vue-property-decorator";

@Component({
  components: {EditOTAModal, AddOTAModal},
})
export default class OTAView extends Vue {
  displayAddModal: boolean = false;
  displayEditModal: boolean = false;
  searchDate = null;
  searchString: string = "";
  data: OTA[] = [];
  originalData: OTA[] = [];
  editId: number = 0;

  sortDate(a, b) {
    return Date.parse(a.created_time) > Date.parse(b.created_time);
  }

  mounted(): void {
    this.getData();
  }

  getData(): void {
    let query = null;
    if (!this._.isEmpty(this.searchDate)) {
      const searchDate = formatTimeRange(this.searchDate);
      query = paramsToQuery({gte: searchDate[0], lte: searchDate[1]});
    }
    this.api.getOTAList({query, sort_by: "created_time:1"}).then((res) => {
      this.originalData = res;
      this.data = this.originalData;
    });
  }

  inputString(str: string): void {
    if (str === "") {
      this.data = this.originalData;
    } else {
      this.data = this.originalData.filter((it: OTA) => {
        return Object.values(it).some((val) => {
          if (typeof val === "string") {
            return val.toUpperCase().includes(str.toUpperCase());
          }
          return false;
        });
      });
    }
  }

  editOTA(id: number): void {
    this.editId = id;
    this.displayEditModal = true;
  }

  deleteOTA(id: number): void {
    this.$confirm("Do you want to delete this OTA?", "Delete", {})
      .then(() => {
        this.api.deleteOTA(id).then(() => {
          this.getData();
          this.$message({
            type: "success",
            message: "success!",
          });
        });
      })
      .catch(() => {
        this.$message({
          type: "info",
          message: "cancel",
        });
      });
  }
}
