











































import {AppContext} from "@/core/api/AppContext";
import {JSONWebToken} from "@/types/ApiTypes";
import {PERMISSION_ACTION, PermissionActions} from "@/types/store/PermissionStore";
import {Component, Vue} from "vue-property-decorator";
import {Action} from "vuex-class";

@Component
export default class LoginView extends Vue {
  @Action(PERMISSION_ACTION.UPDATE_PERMISSION)
  updatePermission!: PermissionActions["UPDATE_PERMISSION"];

  formData: JSONWebToken = {
    account: "",
    password: "",
  };

  version: string = "1.2019121102";

  redirect(name: string): void {
    this.$router.push({name});
  }

  setLoginData(name: string): void {
    if (name === "ROOT") {
      this.formData = {
        account: "root@gmail.com",
        password: "123456",
      };
    } else if (name === "ADMIN") {
      this.formData = {
        account: "admin@gmail.com",
        password: "123456",
      };
    } else {
      this.formData = {
        account: "user@gmail.com",
        password: "123456",
      };
    }
  }

  login(): void {
    this.api.login(this.formData).then((res) => {
      if (!res.hasOwnProperty("errorMessage")) {
        this.updatePermission(res);
        AppContext.buildApiExecutor();
        this.$router.push({name: "App"});
        this.$message({
          type: "success",
          message: "Login Success!",
        });
      } else {
        this.$message({
          type: "error",
          message: "Login Failure!",
        });
      }
    });
  }
}
