


















import DbSettingsInputView from "@/views/transducer-db-settings/DbSettingsInputView.vue";
import {Component, Vue} from "vue-property-decorator";

@Component
export default class TransducerDbSettingsView extends Vue {
  tabs = [{name: "Input", component: DbSettingsInputView}, {name: "Outputs", component: DbSettingsInputView}];

  activeTab: string = this.tabs[0].name;

  serial_num: string = "";

  beforeMount(): void {
    this.serial_num = String(this.$route.query.sn);
  }
}
