






















































import AddProjectBySeqModal from "@/components/project/AddProjectBySeqModal.vue";
import AddSequenceModal from "@/components/sequence/AddSequenceModal.vue";
import {paramsToQuery} from "@/core/utils/paramsToQuery";
import {GlobalTag, SequenceManager} from "@/types/ApiTypes";
import {Component, Vue} from "vue-property-decorator";
import {Getter} from "vuex-class";
import {SequenceJSON} from "@/types/SequenceJSON";
@Component({
  components: {AddSequenceModal, AddProjectBySeqModal},
})
export default class GlobalSequenceView extends Vue {
  @Getter("permission/status") readonly status!: number;
  data: SequenceManager[] = [];
  displayAddProjModal: boolean = false;
  displayAddModal: boolean = false;
  duplicateData: SequenceManager = null;
  seq: number = 0;
  seqName: string = "";
  tagList: GlobalTag[] = [];

  sortDate(a, b) {
    return Date.parse(a.created_time) > Date.parse(b.created_time);
  }

  computeTaskNum(seq: SequenceManager): string {
    const content: SequenceJSON = seq.content ? JSON.parse(seq.content) : null;
    return content ? String(content.tasks.length) : "0";
  }

  computeEventNum(seq: SequenceManager): string {
    if (seq.content === null) {
      return "0";
    }
    const content: SequenceJSON = JSON.parse(seq.content);
    const eventsNumber =
      content.tasks.length > 0
        ? content.tasks.map((task) => task.events.length).reduce((a: number, b: number) => a + b)
        : 0;
    return String(eventsNumber);
  }

  displayStatus(data: SequenceManager): string {
    return data.is_disable ? "Enable" : "Disable";
  }

  changeStatus(data: SequenceManager): void {
    this.api.editSm({...data, is_disable: !data.is_disable}, data.id).then(() => this.getData());
  }

  mounted(): void {
    this.getData();
  }

  displayTag(tags: string): string {
    const idList = tags ? tags.split(",").filter((tag) => tag !== "") : [];
    const tagList = idList
      .map((id) => {
        const tag = this.tagList.find((tag) => String(tag.id) === id);
        if (tag) {
          return tag.name;
        }
        return null;
      })
      .filter((tag) => tag !== null);
    return tagList.join(",");
  }

  openAddModal(seq: SequenceManager): void {
    this.displayAddProjModal = true;
    this.seq = seq.id;
    this.seqName = seq.sequence_name;
  }

  getData(): void {
    const query = paramsToQuery({is_customize: "False"});
    this.api.getSmList({query, sort_by: "created_time:1"}).then((res) => {
      this.data = res;
    });
    this.api.getTagList(null).then((tagList) => {
      this.tagList = tagList;
    });
  }

  duplicateSequence(seq: SequenceManager): void {
    this.duplicateData = seq;
    this.displayAddModal = true;
  }

  cleanDuplicated(): void {
    this.displayAddModal = false;
    this.duplicateData = null;
  }
}
