














































import AddPlanModal from "@/components/plan-management/AddPlanModal.vue";
import EditPlanModal from "@/components/plan-management/EditPlanModal.vue";
import {PlanManagement} from "@/types/ApiTypes";
import {Component, Vue} from "vue-property-decorator";

@Component({
  components: {EditPlanModal, AddPlanModal},
})
export default class PlanManagementView extends Vue {
  displayAddModal: boolean = false;
  displayEditModal: boolean = false;

  editId: number = 0;
  searchString: string = "";
  data: PlanManagement[] = [];
  originalData: PlanManagement[] = [];

  mounted(): void {
    this.getData();
  }

  getData(): void {
    this.api.getPlanList().then((res) => {
      this.originalData = res;
      this.data = this.originalData;
    });
  }

  editPlan(id: number): void {
    this.editId = id;
    this.displayEditModal = true;
  }

  deletePlan(id: number): void {
    this.$confirm("Do you want to delete this plan?", "Delete", {})
      .then(() => {
        this.api.deletePlanManagement(id).then(() => {
          this.getData();
          this.$message({
            type: "success",
            message: "success!",
          });
        });
      })
      .catch(() => {
        this.$message({
          type: "info",
          message: "cancel",
        });
      });
  }

  closeEdit(): void {
    this.displayEditModal = false;
    this.editId = 0;
  }

  inputString(str: string): void {
    if (str === "") {
      this.data = this.originalData;
    } else {
      this.data = this.originalData.filter((it: PlanManagement) => {
        return Object.values(it).some((val) => {
          if (typeof val === "string") {
            return val.toUpperCase().includes(str.toUpperCase());
          }
          return false;
        });
      });
    }
  }
}
