




















import {TABS_ACTION, TabsActions} from "@/types/store/TabStore";
import {Component, Vue, Watch} from "vue-property-decorator";
import {Action, Getter} from "vuex-class";

@Component
export default class NavMenu extends Vue {
  @Getter("permission/status") readonly status!: number;

  @Action(TABS_ACTION.UPDATE_CURRENT_TAB)
  updateTab!: TabsActions["UPDATE_CURRENT_TAB"];

  @Watch("$route", {immediate: true, deep: true})
  onUrlChange(newVal: any) {
    const tabName = newVal.query.tab;
    const tabs = [].concat(this.userTabs, this.adminTabs, this.rootTabs);
    let tab = tabs.find((tab) => tab.name == tabName && (tab.permission == -1 || tab.permission == this.status));
    if (!tab) {
      tab = this.status === 2 ? this.rootTabs[0] : this.userTabs[0];
      this.$router.push({query: {tab: tab.name}});
    } else {
      this.updateTab(tab.name);

      if (tab.index < 0) {
        this.currentIndex = this.userTabs[0].index;
        this.defaultMenuName = this.userTabs[0].name;
      } else {
        this.currentIndex = tab.index;
        this.defaultMenuName = tab.name;
      }
    }
  }

  goToTab(tab: any): void {
    this.$router.push({query: {tab: tab.name}});
  }

  currentIndex: number = 0;
  defaultMenuName: string = "";

  get isRoot(): boolean {
    return this.status === 2;
  }

  get upperTabs(): any[] {
    return this.isRoot
      ? []
      : this.userTabs.filter((tab) => {
          return tab.index > -1 && (tab.permission === this.status || tab.permission === -1);
        });
  }

  get lowerTabs(): any[] {
    return this.isRoot
      ? this.rootTabs
      : this.adminTabs.filter((tab) => {
          return tab.permission === this.status || tab.permission === -1;
        });
  }

  // move to vuex store when api done
  userTabs = [
    {index: 0, title: "My Labs", name: "Lab", permission: -1},
    {index: 1, title: "Sequence Manager", name: "SequenceManager", permission: 1},
    {index: 2, title: "Sequence Manager", name: "UserSequenceManager", permission: 0},
    {index: 3, title: "Sequence Store", name: "SequenceStore", permission: 1},
    {index: 4, title: "Sequence Store", name: "UserSequenceStore", permission: 0},
    {index: -1, title: "Project Manager", name: "ProjectManager", permission: -1},
    {index: -2, title: "Project Worker", name: "ProjectWorker", permission: -1},
    {index: -3, title: "Transducer DB Settings", name: "TransducerDbSettings", permission: -1},
    {index: 8, title: "Transducer Database", name: "TransducerDbDevice", permission: -1},
    {index: -4, title: "Plan Setting", name: "PlanSetting", permission: 0},
  ];

  adminTabs = [
    {index: 9, title: "User Account Manager", name: "UserAccountManagement", permission: 1},
    {index: 10, title: "Plan Manager", name: "PlanManagement", permission: 1},
    {index: 12, title: "Device Manager", name: "IoT", permission: 1},
    {index: 13, title: "OTA Server", name: "OTA", permission: 1},
  ];

  rootTabs = [
    {index: 14, title: "Admin Account Manager", name: "AdminAccountManagement", permission: 2},
    {index: 15, title: "Device Manager", name: "IoT", permission: 2},
  ];
}
