

































import {VerifyMail} from "@/types/ApiTypes";
import {Component, Vue} from "vue-property-decorator";

@Component
export default class ForgetPasswordView extends Vue {
  formData: VerifyMail = {
    activated_link: "",
    mail: "",
  };

  redirect(name: string): void {
    this.$router.push({name});
  }

  forget(): void {
    this.api.postForgetPasswordMail(this.formData).then(() => {
      this.$alert("Already reset your password", "Reset Password").then(() => this.redirect("LoginView"));
    });
  }
}
