
















import GlobalTagsView from "@/views/sequence-store/GlobalTagsView.vue";
import GlobalSequenceView from "@/views/sequence-store/GlobalSequenceView.vue";
import SelfSequenceView from "@/views/sequence-store/SelfSequenceView.vue";
import {Component, Vue} from "vue-property-decorator";
import {Getter} from "vuex-class";

@Component
export default class SequenceStoreView extends Vue {
  @Getter("permission/status") readonly status!: number;

  get tabs() {
    return this.status === 1
      ? [
          {name: "PAL Global Sequence", component: GlobalSequenceView},
          {name: "PAL Customized Sequence", component: SelfSequenceView},
          {name: "Global Tags", component: GlobalTagsView},
        ]
      : [
          {name: "PAL Global Sequence", component: GlobalSequenceView},
          {name: "PAL Customized Sequence", component: SelfSequenceView},
        ];
  }

  activeTab: string = this.tabs[0].name;
}
