











































































import AddProjectModal from "@/components/project/AddProjectModal.vue";
import ProjectReportModal from "@/components/project/ProjectReportModal.vue";
import {formatTimeRange} from "@/core/utils/formatTimeRange";
import {paramsToQuery} from "@/core/utils/paramsToQuery";
import {ProjectManager, ProjectManagerSerializerWithPopulate} from "@/types/ApiTypes";
import {SequenceEvent, SequenceJSON} from "@/types/SequenceJSON";
import {TABS_ACTION, TabsActions} from "@/types/store/TabStore";
import {Component, Vue} from "vue-property-decorator";
import {Action, Getter} from "vuex-class";

@Component({
  components: {ProjectReportModal, AddProjectModal},
})
export default class ProjectManagerView extends Vue {
  @Getter("permission/email") readonly email!: string;
  @Action(TABS_ACTION.UPDATE_CURRENT_TAB)
  updateTab!: TabsActions["UPDATE_CURRENT_TAB"];
  filename: string = null;
  displayAddModal: boolean = false;
  displayReportModal: boolean = false;
  searchDate = null;
  labId: string = "";
  data: ProjectManager[] = [];
  timer: any = null;

  goToTab(id: number): void {
    this.$router.push({query: {tab: "ProjectWorker", id: String(id)}});
    this.updateTab("ProjectWorker");
  }

  beforeMount(): void {
    this.labId = String(this.$route.query.id);
    this.getData();
  }

  getData(): void {
    let query = null;
    if (!this._.isEmpty(this.searchDate)) {
      const searchDate = formatTimeRange(this.searchDate);
      query = {...query, gte: searchDate[0], lte: searchDate[1]};
    }
    query = paramsToQuery({...query, lab: this.labId});
    this.api.getProjectList({query}).then((res) => {
      this.data = res;
    });
  }

  load(project: any): void {
    this.$confirm(`Are you sure ?`, "Load project", {
      confirmButtonText: "save",
      cancelButtonText: "cancel",
    });
  }

  report(project: any): void {
    this.displayReportModal = true;
  }

  remove(project: ProjectManager): void {
    this.$confirm(`Are you sure delete ${project.name} ?`, "Delete project", {
      confirmButtonText: "save",
      cancelButtonText: "cancel",
    }).then(() => {
      this.api.deleteProjectManagement(project.id).then(() => {
        this.getData();
        this.$message({
          type: "success",
          message: "Success!",
        });
      });
    });
  }

  getReturn(project: ProjectManagerSerializerWithPopulate): void {
    const id = project.lab.device_manager.serial_num;
    this.api.getPALSendCommand({id, cmd: "version"}).then(() => {
      this.timer = setTimeout(() => {
        this.api.getPALGetReturn({id}).then((res) => {
          this.$alert(res);
          clearTimeout(this.timer);
        });
      }, 1000);
    });
  }

  async runProject(project: ProjectManagerSerializerWithPopulate): Promise<void> {
    const tasks = (JSON.parse(project.sequence.content) as SequenceJSON).tasks;
    for (let taskIndex = 0; taskIndex < tasks.length; taskIndex++) {
      const events = tasks[taskIndex].events;
      for (let eventIndex = 0; eventIndex < events.length; eventIndex++) {
        const event = events[eventIndex];
        await this.doEvent(event, project.lab.device_manager.serial_num);
	  }
    }
  }

  delay = (time: number) => {
    return new Promise((resolve) => {
      setTimeout(resolve, time);
    });
  };

  async doEvent(event: SequenceEvent, sn: string): Promise<void> {
    if (event.type === "Measurement") {
      await this.doMeasurementEvent(event, sn);
    } else if (event.type === "Pause") {
      await this.doPauseEvent(event);
    } else if (event.type === "Analysis") {
	  await this.doAnalysisEvent();
    } else if (event.type === "Calculation") {
      await this.doCalculationEvent().then();
    }
  }

  async doMeasurementEvent(event, sn: string): Promise<void> {
    const checkPlayback = event.playbackChannelData.length > 0;
    const checkRecord = event.recordingData.length > 0;
    if (checkRecord && checkPlayback) {
      await this.doRecordAndPlayback(event, sn).then(async () => {
        await this.delay(event.eventLength * 1000);
        // this.doStop(sn);
      });
    } else if (checkRecord) {
      await this.doRecord(event, sn).then(async () => {
        await this.delay(event.eventLength * 1000);
        // this.doStop(sn);
	  });
      await this.api.s3upload({id: sn, src: `${this.filename}.wav`, dst: `s3://${this.email}/${this.filename}.wav`});
    } else {
      console.warn("no record and playback setting");
    }
  }

  doStop(sn: string): Promise<string> {
    const command = "stop";
    return this.api.getPALSendCommand({id: sn, cmd: command});
  }

  doRecord(event, sn: string): Promise<string> {
    const Inputs = event.recordingData.map((record) => {
      this.filename = record.name;
      return {Channel: record.channelIndex, FilePath: record.name + ".wav", Title: record.name};
    });
    const params = {
      Base64Duration: btoa(String(event.eventLength)),
      Inputs,
    };
    const command = "record;" + JSON.stringify(params);
    return this.api.getPALSendCommand({id: sn, cmd: command});
  }

  doRecordAndPlayback(event, sn: string): Promise<string> {
    const Inputs = event.recordingData.map((record) => {
      return {Channel: record.channelIndex, FilePath: record.name + ".wav", Title: record.name};
    });
    const Outputs = event.playbackChannelData.map((playback) => {
      return {
        Channel: playback.channelIndex,
        FilePath: playback.sourceFileName,
        Base64LevelAdjustment: btoa(String(playback.adjustment)),
      };
    });
    const params = {
      Base64Duration: btoa(String(event.eventLength)),
      Inputs,
      Outputs,
    };

    const command = "record;" + JSON.stringify(params);
    return this.api.getPALSendCommand({id: sn, cmd: command});
  }

  async doPauseEvent(event): Promise<void> {
    await this.delay(event.eventLength * 1000);
  }

  async doAnalysisEvent(): Promise<void> {
    // const mockParams = {
    //   parameters: {
    //     "Frequency Weighting": "None",
    //     "Time Weighting": "Fast",
    //     "Downsampling Rate": "32",
    //   },
    //   inputs: [
    //     {
    //       "Bucket Name": "ecvtestbucket",
    //       "jwt.token": "{cer}",
    //       "Object Key": "500Hz.wav",
    //       "Take Time": "10",
    //       "Skip Time": "0",
    //     },
    //   ],
    //   output: {
    //     "Bucket Name": "ecvtestbucket",
    //     "Object Key": "500Hz_LevelVersusTime.rst",
    //   },
    // };
    const mockParams = {
      parameters: [
        {name: "Time Weighting", value: "Fast"},
        {name: "Frequency Weighting", value: "None"},
        {name: "Downsampling Rate", value: "16"},
      ],
      inputs: [
        [
          {name: "Bucket Name", value: "pal.data"},
          {name: "Object Key", value: `${this.email}/${this.filename}.wav`},
          {name: "Skip Time", value: "0"},
          {name: "Take Time", value: "-1"},
        ],
      ],
      output: [
        {name: "Bucket Name", value: "pal.data"},
        {name: "Object Key", value: `${this.email}/LevelVersusTime.rst`},
      ],
    };
    this.api.doLevelTimeApi(2, mockParams).then();
  }

  async doCalculationEvent(): Promise<void> {
    const mockParams = {
      parameters: [{name: "Result Number", value: "1"}, {name: "Formula", value: "20*log10(x)"}],
      inputs: [
        [{name: "Bucket Name", value: "pal.data"}, {name: "Object Key", value: `${this.email}/FourierSpectrum.rst`}],
      ],
      output: [
        {name: "Bucket Name", value: "pal.data"},
        {name: "Object Key", value: `${this.email}/FourierSpectrum_CurveMathematics.rst`},
      ],
    };

    this.api.doCalculateCurveApi(2, mockParams).then((res) => {
      this.$message.success(`Calculation: ${res}`);
    });
  }
}
