






































































































































































import AddLabModal from "@/components/user-account-management/AddLabModal.vue";
import EditLabModal from "@/components/user-account-management/EditLabModal.vue";
import {initialUserAccount} from "@/core/constants/api/initialUserAccount";
import {Account, Lab, LabSerializerWithPopulate, PlanManagement} from "@/types/ApiTypes";
import {Form} from "element-ui";
import {Component, Emit, Mixins, Watch} from "vue-property-decorator";
import AsModal from "../../mixins/AsModal";

@Component({
  components: {EditLabModal, AddLabModal},
})
export default class AddUserAccountModal extends Mixins(AsModal) {
  @Emit("refresh") refresh(): void {}

  @Watch("visible")
  onVisibleChange(val: boolean): void {
    if (val) {
      this.getData();
    }
  }

  displayAddLabModal: boolean = false;
  displayEditLabModal: boolean = false;
  formData: Partial<Account> = {...initialUserAccount()};
  labList: LabSerializerWithPopulate[] = [];
  planList: PlanManagement[] = [];
  selectedLab: LabSerializerWithPopulate = null;
  duplicateLab: Lab = null;

  addLab(id: number) {
    if (id) {
      this.formData.labs.push(id as any);
    }
    const ids = this.formData.labs.join(",");
    this.api.getLabList({ids}).then((res) => {
      this.labList = res;
    });
  }

  copyLab(lab: LabSerializerWithPopulate): void {
    const copyLab = {...this._.pick(lab, ["description", "name"])};
    this.$copyText(JSON.stringify(copyLab));
    this.$message({message: "Lab copied", type: "success"});
  }

  getData(): void {
    this.api.getPlanList({fields: "id,plan_name"}).then((res) => {
      this.planList = res;
    });
  }

  submit(): void {
    (this.$refs.form as Form).validate((valid) => {
      if (valid) {
        this.api.addUserAccount(this.formData).then(() => {
          this.refresh();
          this.cancel();
        });
      } else {
        return false;
      }
    });
  }

  cancel(): void {
    (this.$refs.form as Form).resetFields();
    this.formData = {...initialUserAccount()};
    this.dismiss();
  }

  editLab(lab: LabSerializerWithPopulate): void {
    this.selectedLab = lab;
    this.displayEditLabModal = true;
  }
  removeLab(index: number): void {
    this.labList.splice(index, 1);
  }

  importLab(): void {
    navigator.clipboard.readText().then((text) => {
      this.duplicateLab = JSON.parse(text);
      this.displayAddLabModal = true;
    });
  }

  cleanDuplicated(): void {
    this.displayAddLabModal = false;
    this.duplicateLab = null;
  }

  rules = {
    name: [{required: true, message: "User Name is required.", trigger: "change"}],
    account: [{required: true, message: "User Email is required.", trigger: "change"}],
    company_id: [{required: true, message: "Company Id is required.", trigger: "change"}],
    company_name: [{required: true, message: "Company Name is required.", trigger: "change"}],
    company_address: [{required: true, message: "Company address is required.", trigger: "change"}],
    plan_management: [{required: true, message: "Plan is required.", trigger: "change"}],
    billing_status: [{required: true, message: "Billing status is required.", trigger: "change"}],
    certificate_of_registration: [
      {required: true, message: "Certificate of registration is required.", trigger: "change"},
    ],
    quota: [{required: true, message: "Quota is required.", trigger: "change"}],
  };
}
