





























import AsModal from "@/mixins/AsModal";
import {Component, Emit, Mixins, Prop} from "vue-property-decorator";

@Component
export default class AddResultModal extends Mixins(AsModal) {
  @Emit("refresh") refresh(data: object): void {}
  @Prop({type: Array, default: () => []}) readonly measurementsource;
  
  clean(): void {
    this.resetForm();
    this.dismiss();
  }

  resetForm(): void {
    this.$nextTick(() => {
      const ref: any = this.$refs["resultRuleFormRef"];
      ref.resetFields();
    });
    this.resultRuleForm = {
      name: "",
      resource: "",
      skipTime: 5,
      takeLength: 10,
    };
  }

  addResult(): void {
    const ref: any = this.$refs["resultRuleFormRef"];
    ref.validate((valid) => {
      if (valid) {
        this.refresh(this.resultRuleForm);
        this.clean();
      } else {
        return false;
      }
    });
  }

  resultRuleForm = {
    name: "",
    resource: "",
    skipTime: 5,
    takeLength: 10,
  };
  resultRule = {
    name: [{required: true, message: "Result name is required.", trigger: "change"}],
    resource: [{required: true, message: "Data source is required.", trigger: "change"}],
  };
}
