import {Component, Emit, Mixins, Prop} from "vue-property-decorator";

@Component({
  components: {
    AddCalculationResultModal,
    AddRequirementModal,
  },
})
export default class EventCalculationModal extends Mixins(AsModal) {
  @Emit("refresh") refresh(): void {}
  @Prop({type: Object, default: () => null}) readonly data;
  @Prop({type: Array, default: () => []}) readonly analysissource;

  mounted() {
    if (this.data) {
	  console.log(JSON.stringify(this.data));
	  this.eventData = Object.assign(this.eventData, this.data);
	  // this.eventData = this._.pick(this.data, ["calculationName", "results", "requirements", "expression", "parameters"]);
    }
  }

  clean(): void {
    this.dismiss();
  }

  calculationName = [
    {
      title: "Curve Smoothing",
      category: ["All", "Smoothing"],
      infoUrl: "https://g5d9ich7a6.execute-api.ap-southeast-1.amazonaws.com/Prod/info",
    },
    {
      title: "Curve Mathematics",
      category: ["All", "Arithmetic"],
      infoUrl: "https://qttf07pvz9.execute-api.ap-southeast-1.amazonaws.com/Prod/info",
    },
  ];

  eventData = {
    calculationName: "",
    results: [],
    requirements: [],
    expression: {
      xMin: "",
      xMax: "",
      xScale: "",
      yMin: "",
      yMax: "",
	  yScale: "",
	  xRange: [],
	  yRange: []
	},
	parameters: []
  };

  eventFields = [
    {
      title: "Calculation Category",
      type: "select",
      options: [{title: "All"}, {title: "Smoothing"}, {title: "Arithmetic"}],
      default: "All",
      placeholder: "",
      value: "All",
    },
    {
      title: "Calculation Name",
      type: "select",
      options: [], // calculationNameFilterByCategory
      default: "",
      placeholder: "",
      value: "",
    },
  ];

  get calculationNameFilterByCategory(): any[] {
    return this.calculationName.filter((option) => {
      return option.category.some((category) => {
        return category == this.eventFields[0].value;
      });
    });
  }

  getEventInfo(title: string): void {
    switch (title) {
      case "Curve Smoothing": {
        this.eventInfo = this.mockEventInfo[0];
        break;
      }
      case "Curve Mathematics": {
        this.eventInfo = this.mockEventInfo[1];
        break;
      }
      default: {
        this.eventInfo = {};
        break;
      }
    }
  }

  mockEventInfo = [
    {
      Name: "Curve Smoothing",
      ModuleType: 1,
      MinimumInputs: 1,
      MaximumInputs: 1,
      InputFormat: {
        "Bucket Name": {
          Description: "The Amazon S3 bucket where the source file is located",
        },
        "Object Key": {
          Description: "The key of the source file",
        },
      },
      OutputFormat: {
        "Bucket Name": {
          Description: "The Amazon S3 bucket where the destination file is located",
        },
        "Object Key": {
          Description: "The key of the destination file",
        },
      },
      ParameterFormat: [{
		  Name: "Smoothing Order",
          DefaultValue: "1/6",
          Options: ["1/1", "1/3", "1/6", "1/12", "1/24"],
          Description: "",
        },
	  ],
      Description: "Smooth the given curve in dB domain",
    },
    {
      Name: "Curve Mathematics",
      ModuleType: 1,
      Version: "1.0",
      MinimumInputs: 1,
      MaximumInputs: 1,
      InputFormat: {
        "Bucket Name": {
          Description: "The Amazon S3 bucket where the source file is located",
        },
        "Object Key": {
          Description: "The key of the source file",
        },
      },
      OutputFormat: {
        "Bucket Name": {
          Description: "The Amazon S3 bucket where the destination file is located",
        },
        "Object Key": {
          Description: "The key of the destination file",
        },
      },
      ParameterFormat: [{
		  Name: "Input Number",
          DefaultValue: "",
          Description: "",
        }, {
		  Name: "Formula",
          DefaultValue: "",
          Description:
            "Operators: +, -, *, /, ^, !, #Functions: sin(x), cos(x), tan(x), asin(x), acos(x), atan(x), sqrt(x), log2(x), log10(x), ln(x)...Constants: pi, e",
        },
      ],
      Description: "Apply mathematical calculations to the input data",
    },
  ];

  eventInfo = {};
  displayAddResultModal: boolean = false;
  displayAddRequirementModal: boolean = false;
  deleteResultData(index: number): void {
    this.eventData.results.splice(index, 1);
  }

  deleteRequirementData(index: number): void {
    this.eventData.requirements.splice(index, 1);
  }

  addResultData(data: object) {
    if (data) {
      this.eventData.results.push({...data} as any);
    }
  }

  addRequirementData(data: object) {
    if (data) {
      this.eventData.requirements.push({...data} as any);
    }
  }
}
