



























import AsModal from "@/mixins/AsModal";
import {Component, Emit, Mixins} from "vue-property-decorator";
import {buildImportSequence} from "@/core/utils/buildImportSequence";

@Component
export default class ImportSequenceModal extends Mixins(AsModal) {
  @Emit("refresh") refresh(): void {}

  formData = {sequence_name: "", content: "", note: ""};
  fileList = [];

  add(): void {
    this.api.addSm({...this.formData}).then(() => {
      this.refresh();
      this.clean();
    });
  }

  clean(): void {
    this.formData = {sequence_name: "", content: "", note: ""};
    this.dismiss();
  }

  buildImportSequence(content: string): void {
    this.formData.content = buildImportSequence(content);
  }

  upload(options: {file: File}): void {
    const fileReader = new FileReader();
    fileReader.readAsText(options.file);
    fileReader.onload = () => {
      this.buildImportSequence(fileReader.result as string);
    };
  }
}
