




























































import CalibrationEventModal from "@/components/events/CalibrationEventModal.vue";
import AddTransducerModal from "@/components/transcuder-db-device/AddTransducerModal.vue";
import SelectTransducerModal from "@/components/transcuder-db-device/SelectTransducerModal.vue";
import {DateFormat} from "@/core/constants/DateFormat";
import {formatDate} from "@/core/utils/formatDate";
import {paramsToQuery} from "@/core/utils/paramsToQuery";
import {ChannelSerializerWithPopulate} from "@/types/ApiTypes";
import {Component, Prop, Vue} from "vue-property-decorator";

@Component({
  components: {CalibrationEventModal, SelectTransducerModal, AddTransducerModal},
})
export default class DbSettingsInputView extends Vue {
  @Prop({type: String, default: () => ""}) readonly tabName!: string;
  labId: string = "";
  chNum: string = "";
  data: ChannelSerializerWithPopulate[] = [];
  displaySelectModal: boolean = false;
  displayCalibrationEventModal: boolean = false;
  displayCalibrationEqualization: boolean = false;
  selectedChannel: ChannelSerializerWithPopulate = null;
  selectedIndex: number = null;
  serial_num: string = "";
  sensitivity: number = null;
  beforeMount(): void {
    this.labId = String(this.$route.query.id);
    this.chNum = String(this.$route.query.ch);
    this.serial_num = String(this.$route.query.sn);
    this.getData();
  }

  getData(): void {
    this.api.getTDBSettingList({query: paramsToQuery({lab: this.labId})}).then(async (res) => {
      if (res.length === 0) {
        this.api.addTransducerDBSetting({lab: Number(this.labId)}).then(async (transducer) => {
          const channelIds = [];
          for (let i = 1; i <= Number(this.chNum) * 2; i++) {
            await this.api.addChannels({channel_num: String(i)}).then((res) => {
              channelIds.push(res.id);
            });
          }
          this.api.editTransducerDBSetting({channel: channelIds}, transducer.id).then(() => this.getData());
        });
      } else {
        const tdbs = res[0];
        if (Number(this.chNum) * 2 > tdbs.channel.length) {
          const channelIds = [];
          for (let i = 1; i <= Number(this.chNum) * 2; i++) {
            await this.api.addChannels({channel_num: String(i)}).then((res) => {
              channelIds.push(res.id);
            });
          }
          this.api.editTransducerDBSetting({channel: channelIds}, tdbs.id).then(() => this.getData());
        } else {
          if (this.tabName === "Input") {
            this.data = tdbs.channel.slice(0, Number(this.chNum));
          } else {
            this.data = tdbs.channel.slice(Number(this.chNum), Number(this.chNum) * 2);
          }
        }
      }
    });
  }

  selectTransducer(channel: ChannelSerializerWithPopulate, index: number): void {
    if (this.selectedIndex !== index) this.sensitivity = null;
    this.selectedChannel = channel;
    this.selectedIndex = index;
    this.displaySelectModal = true;
  }

  clearChannel(channel: ChannelSerializerWithPopulate): void {
    this.api.editChannel({transducerdb: null}, channel.id).then(() => this.getData());
  }

  displayCalibration(isEqualization: boolean, index: number, channel: ChannelSerializerWithPopulate): void {
    this.selectedChannel = channel;
    this.selectedIndex = index;
    this.displayCalibrationEqualization = isEqualization;
    this.displayCalibrationEventModal = true;
  }

  displayContent(channel: ChannelSerializerWithPopulate): string {
    if (channel.transducerdb) {
      const {calibration_date, name, category} = channel.transducerdb;
      return `Category: ${category} / Name: ${name} / Calibration Date: ${formatDate(
        calibration_date,
        DateFormat.YMD,
      )}`;
    } else {
      return "";
    }
  }

  doCalibrate(cb: any): void {
    this.sensitivity = cb["decode"];

    if (this.selectedChannel.transducerdb !== null) {
      if (this.tabName !== "Input") {
        // Config Output
        const params = {
          ChannelType: "Output",
          ChannelIndex: this.selectedIndex,
          TransducerName: name,
          Base64Sensitivity: this.encode(this.sensitivity),
          SensitivityUnit: this.selectedChannel.transducerdb.signal_type, //btoa(String(this.selectedChannel.transducerdb.sensitivity_unit_pa_per_v)),
          // Base64Coefficients: Base64Coefficients,  //CoefficientsFile
        };
        const command = "config;" + JSON.stringify(params);
        this.api.getPALSendCommand({id: this.serial_num, cmd: command}).then(() => {
          this.api
            .editChannel({transducerdb: this.selectedChannel.transducerdb.id}, this.selectedChannel.id)
            .then(() => {
              this.getData();
            });
        });
      } else {
        // Config Input
        const params = {
          ChannelType: "Input",
          ChannelIndex: this.selectedIndex,
          TransducerName: name,
          Base64Sensitivity: this.encode(this.sensitivity),
          SensitivityUnit: this.selectedChannel.transducerdb.signal_type, //btoa(String(this.selectedChannel.transducerdb.sensitivity_unit_pa_per_v)),
          IsPhantomPowerOn: true,
        };
        const command = "config;" + JSON.stringify(params);
        this.api.getPALSendCommand({id: this.serial_num, cmd: command}).then(() => {
          this.api
            .editChannel({transducerdb: this.selectedChannel.transducerdb.id}, this.selectedChannel.id)
            .then(() => {
              this.getData();
            });
        });
      }
    }
  }

  encode(float64Number): string {
    const keyString = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789+/=";

    const arrayBuffer = new ArrayBuffer(8);
    const floatView = new Float64Array(arrayBuffer);
    const bytes = new Uint8Array(arrayBuffer);
    const byteLength = bytes.byteLength;
    const byteRemainder = byteLength % 3;
    const mainLength = byteLength - byteRemainder;

    floatView[0] = float64Number;

    let base64 = "";
    let a, b, c, d;
    let chunk;

    for (let i = 0; i < mainLength; i = i + 3) {
      chunk = (bytes[i] << 16) | (bytes[i + 1] << 8) | bytes[i + 2];

      a = (chunk & 16515072) >> 18;
      b = (chunk & 258048) >> 12;
      c = (chunk & 4032) >> 6;
      d = chunk & 63;

      base64 += keyString[a] + keyString[b] + keyString[c] + keyString[d];
    }

    if (byteRemainder == 1) {
      chunk = bytes[mainLength];

      a = (chunk & 252) >> 2;
      b = (chunk & 3) << 4;

      base64 += keyString[a] + keyString[b] + "==";
    } else if (byteRemainder == 2) {
      chunk = (bytes[mainLength] << 8) | bytes[mainLength + 1];

      a = (chunk & 64512) >> 10;
      b = (chunk & 1008) >> 4;
      c = (chunk & 15) << 2;

      base64 += keyString[a] + keyString[b] + keyString[c] + "=";
    }

    return base64;
  }
}
