











































import EventAnalysisModal from "@/components/events/EventAnalysisModal.vue";
import EventCalculationModal from "@/components/events/EventCalculationModal.vue";
import EventMeasureModal from "@/components/events/EventMeasureModal.vue";
import EventPauseModal from "@/components/events/EventPauseModal.vue";
import AsModal from "@/mixins/AsModal";
import {SequenceEvent} from "@/types/SequenceJSON";
import {Component, Emit, Mixins, Prop, Watch} from "vue-property-decorator";

@Component({
  components: {EventAnalysisModal, EventCalculationModal, EventMeasureModal, EventPauseModal},
})
export default class EditEventModal extends Mixins(AsModal) {
  @Emit("refresh") refresh(): SequenceEvent {
    return this.currentEvent;
  }
  @Prop({type: Object, default: () => null}) readonly originalEvent!: SequenceEvent;
  @Prop({type: Array, default: () => []}) readonly measurementsource;
  @Prop({type: Array, default: () => []}) readonly analysissource;  

  @Watch("visible")
  onVisibleChange(val: boolean): void {
    if (val && this.originalEvent) {
      this.currentEvent = {...this.originalEvent};
    }
  }

  currentEvent: SequenceEvent = {
    name: "",
    type: "Pause",
    id: 0,
  };

  eventType: object[] = [{title: "Measurement"}, {title: "Pause"}, {title: "Analysis"}, {title: "Calculation"}];

  clean(): void {
    this.currentEvent = {
      name: "",
      type: "Pause",
      id: 0,
    };
    this.dismiss();
  }

  addEvent(): void {
    let refName = "";
    switch (this.currentEvent["type"]) {
      case "Measurement": {
        refName = "measure";
        break;
      }
      case "Pause": {
        refName = "pause";
        break;
      }
      case "Calculation": {
        refName = "calculation";
        break;
      }
      case "Analysis": {
        refName = "analysis";
        break;
      }
      default: {
        refName = "";
        break;
      }
    }
    const ref: any = this.$refs[refName];
    this.currentEvent = {...this.currentEvent, ...ref.eventData};
    this.refresh();
    this.clean();
  }
}
