

































import {initialDeviceManagement} from "@/core/constants/api/initialDeviceManagement";
import {DeviceManagement, DeviceType} from "@/types/ApiTypes";
import {Form} from "element-ui";
import {Component, Emit, Mixins} from "vue-property-decorator";
import AsModal from "../../mixins/AsModal";

@Component
export default class AddDeviceModal extends Mixins(AsModal) {
  @Emit("refresh") refresh(): void {}

  formData: DeviceManagement = {...initialDeviceManagement()};
  hardware_type: DeviceType = null;
  hardwareList: DeviceType[] = [];

  beforeMount(): void {
    this.api.getDeviceTypeList({fields: "id,name"}).then((res) => {
      this.hardwareList = res;
    });
  }
  add(): void {
    (this.$refs.form as Form).validate((valid) => {
      if (valid) {
        this.api.addDeviceManagement(this.formData).then((res) => {
          if (res.hasOwnProperty("errorMessage")) {
            this.$message({
              message: (res as any).errorMessage.serial_num,
              type: "error",
            });
          } else {
            this.refresh();
            this.clean();
          }
        });
      } else {
        return false;
      }
    });
  }

  clean(): void {
    (this.$refs.form as Form).resetFields();
    this.formData = {...initialDeviceManagement()};
    this.dismiss();
  }

  rules = {
    name: [{required: true, message: "Name is required.", trigger: "change"}],
    serial_num: [{required: true, message: "Serial Number is required.", trigger: "change"}],
  };
}
