






















































































import AddUserAccountModal from "@/components/user-account-management/AddUserAccountModal.vue";
import EditUserAccountModal from "@/components/user-account-management/EditUserAccountModal.vue";
import GetInvoiceModal from "@/components/user-account-management/GetInvoiceModal.vue";
import {formatTimeRange} from "@/core/utils/formatTimeRange";
import {paramsToQuery} from "@/core/utils/paramsToQuery";
import {Account, Lab} from "@/types/ApiTypes";
import {Component, Vue} from "vue-property-decorator";
import {Getter} from "vuex-class";

@Component({
  components: {EditUserAccountModal, AddUserAccountModal, GetInvoiceModal},
})
export default class UserAccountManagementView extends Vue {
  @Getter("permission/email") readonly email!: string;

  data: Account[] = [];
  displayGetModal: boolean = false;
  displayAddModal: boolean = false;
  displayEditModal: boolean = false;
  editAcc: Account = null;
  searchDate = null;
  searchString: string = "";
  formData = {status: 0};
  originalData: Account[] = [];

  sortDate(a, b) {
    return Date.parse(a.created_time) > Date.parse(b.created_time);
  }

  mounted(): void {
    this.getData();
  }

  join(labs: Lab[]): string {
    return !this._.isEmpty(labs)
      ? labs
          .map((lab) => {
            return lab.name;
          })
          .join(", ")
      : "";
  }

  getData(): void {
    let params = this._.cloneDeep(this.formData);
    if (!this._.isEmpty(this.searchDate)) {
      const searchDate = formatTimeRange(this.searchDate);
      params = {...params, ...{gte: searchDate[0], lte: searchDate[1]}};
    }
    this.api.getUserAccountList({query: paramsToQuery(params), sort_by: "created_time:1"}).then((res) => {
      this.originalData = res;
      this.data = this.originalData;
    });
  }

  inputString(str: string): void {
    if (str === "") {
      this.data = this.originalData;
    } else {
      this.data = this.originalData.filter((it: Account) => {
        return Object.values(it).some((val) => {
          if (typeof val === "string") {
            return val.toUpperCase().includes(str.toUpperCase());
          }
          return false;
        });
      });
    }
  }

  editAccount(acc: Account): void {
    this.editAcc = acc;
    this.displayEditModal = true;
  }

  closeEdit(): void {
    this.displayEditModal = false;
    this.editAcc = null;
  }

  sendBilling(mail: string): void {
    this.api.postBillingMail({mail, admin_mail: this.email}).then(() => {
      this.$message({message: "Send Billing Email!", type: "success"});
    });
  }
  statusLabel(status: boolean): string {
    return status ? "Disable" : "Enable";
  }

  enableAccount(acc: Account): void {
    this.$confirm("Are you sure?", `${this.statusLabel(acc.has_right)} Account`).then(() => {
      if (!acc.has_right) {
        this.api.postRecoveryMail(acc.account).catch(console.warn);
      }
      this.api
        .patchUserAccount({has_right: !acc.has_right, is_active: !acc.has_right}, acc.id)
        .then(() => this.getData());
    });
  }
}
