import {Tab, Tabs, TABS_MUTATION, TabsActions, TabsMutations} from "@/types/store/TabStore";
import {VuexActions, VuexMutations} from "@/types/store/VuexTypes";
import AdminAccountManagementView from "@/views/AdminAccountManagementView.vue";
import ChangePasswordView from "@/views/ChangePasswordView.vue";
import DefaultView from "@/views/DefaultView.vue";
import IoTView from "@/views/IoTView.vue";
import MyLabsView from "@/views/MyLabsView.vue";
import OTAView from "@/views/OTAView.vue";
import PlanManagementView from "@/views/PlanManagementView.vue";
import PlanSettingView from "@/views/PlanSettingView.vue";
import ProjectManagerView from "@/views/ProjectManagerView.vue";
import ProjectWorkerView from "@/views/ProjectWorkerView.vue";
import SequenceStoreView from "@/views/sequence-store/SequenceStoreView.vue";
import SequenceManagerView from "@/views/SequenceManagerView.vue";
import TransducerDbDeviceView from "@/views/transducer-db-device/TransducerDbDeviceView.vue";
import TransducerDbSettingsView from "@/views/transducer-db-settings/TransducerDbSettingsView.vue";
import UserAccountManagementView from "@/views/UserAccountManagementView.vue";
import {GetterTree} from "vuex";

const namespaced = true;

const state: Tabs = {
  all: [
    {name: "changePassword", tab: ChangePasswordView},
    {name: "UserAccountManagement", tab: UserAccountManagementView},
    {name: "AdminAccountManagement", tab: AdminAccountManagementView},
    {name: "PlanManagement", tab: PlanManagementView},
    {name: "PlanSetting", tab: PlanSettingView},
    {name: "ProjectManager", tab: ProjectManagerView},
    {name: "ProjectWorker", tab: ProjectWorkerView},
    {name: "SequenceManager", tab: SequenceManagerView},
    {name: "UserSequenceManager", tab: SequenceManagerView},
    {name: "SequenceStore", tab: SequenceStoreView},
    {name: "UserSequenceStore", tab: SequenceStoreView},
    {name: "Lab", tab: MyLabsView},
    {name: "TransducerDbSettings", tab: TransducerDbSettingsView},
    {name: "TransducerDbDevice", tab: TransducerDbDeviceView},
    {name: "IoT", tab: IoTView},
    {name: "OTA", tab: OTAView},
  ],
  currentTab: null,
};

const getters: GetterTree<Tabs, any> = {
  all: ({all}) => all,
  currentTab: ({currentTab}) => currentTab,
};

const mutations: VuexMutations<TabsMutations, Tabs> = {
  UPDATE_CURRENT_TAB(tabs, tab): void {
    tabs.currentTab = tab as Tab;
  },
};

const actions: VuexActions<TabsActions, Tabs, any> = {
  UPDATE_CURRENT_TAB: (context, tabName) => {
    const tab = context.state.all.find((t) => t.name === tabName);
    if (tab) {
      context.commit(TABS_MUTATION.UPDATE_CURRENT_TAB, tab);
    } else {
      context.commit(TABS_MUTATION.UPDATE_CURRENT_TAB, {name: "Default", tab: DefaultView});
    }
  },
};

export default {
  namespaced,
  state,
  getters,
  actions,
  mutations,
};
