






























































import AddDeviceModal from "@/components/device-management/AddDeviceModal.vue";
import EditDeviceModal from "@/components/device-management/EditDeviceModal.vue";
import {formatTimeRange} from "@/core/utils/formatTimeRange";
import {paramsToQuery} from "@/core/utils/paramsToQuery";
import {DeviceManagement} from "@/types/ApiTypes";
import {Component, Vue} from "vue-property-decorator";
@Component({
  components: {EditDeviceModal, AddDeviceModal},
})
export default class IoTView extends Vue {
  displayAddModal: boolean = false;
  displayEditModal: boolean = false;
  data: DeviceManagement[] = [];
  originalData: DeviceManagement[] = [];
  searchDate = null;
  searchString: string = "";
  editId: number = 0;

  sortDate(a, b) {
    return Date.parse(a.created_time) > Date.parse(b.created_time);
  }

  mounted(): void {
    this.getData();
  }

  getData(): void {
    let query = null;
    if (!this._.isEmpty(this.searchDate)) {
      const searchDate = formatTimeRange(this.searchDate);
      query = paramsToQuery({gte: searchDate[0], lte: searchDate[1]});
    }
    this.api.getDeviceList({query, sort_by: "created_time:1"}).then((res) => {
      this.originalData = res;
      this.data = this.originalData;
    });
  }

  deleteDevice(id: number): void {
    this.$confirm("Do you want to delete this Device?", "Delete", {})
      .then(() => {
        this.api.deleteDeviceManagement(id).then(() => {
          this.getData();
          this.$message({
            type: "success",
            message: "success!",
          });
        });
      })
      .catch(() => {
        this.$message({
          type: "info",
          message: "cancel",
        });
      });
  }

  editDevice(id: number): void {
    this.editId = id;
    this.displayEditModal = true;
  }

  closeEdit(): void {
    this.displayEditModal = false;
    this.editId = 0;
  }

  inputString(str: string): void {
    if (str === "") {
      this.data = this.originalData;
    } else {
      this.data = this.originalData.filter((it: DeviceManagement) => {
        return Object.values(it).some((val) => {
          if (typeof val === "string") {
            return val.toUpperCase().includes(str.toUpperCase());
          }
          return false;
        });
      });
    }
  }
}
