
























































import {paramsToQuery} from "@/core/utils/paramsToQuery";
import AsModal from "@/mixins/AsModal";
import {ChannelSerializerWithPopulate, TransducerDB} from "@/types/ApiTypes";
import {Component, Emit, Mixins, Prop, Watch} from "vue-property-decorator";

@Component
export default class SelectTransducerModal extends Mixins(AsModal) {
  @Prop({type: String, default: () => ""}) readonly tabName!: string;
  @Prop({type: String, default: () => null}) readonly sn!: string;
  @Prop({type: Object, default: () => null}) readonly data!: ChannelSerializerWithPopulate;
  @Prop({type: Number, default: () => null}) readonly sensitivity!: any;
  @Emit("refresh") refresh(): void {}

  @Watch("visible")
  onVisibleChange(val: boolean): void {
    if (val && this.data) {
      this.category = this.tabName === "Outputs" ? "Actuator" : "Sensor";
      this.channel = this.data;
      if (this.data.transducerdb) {
        this.transducer = this.data.transducerdb as any;
      }
      this.isEdit = true;
      this.sensitivity2 = typeof this.sensitivity == "number" ? this.sensitivity : this.transducer ? this.transducer.sensitivity : null;
      this.onSelectCategory(this.category);
    }
  }
  category: string = "";
  isEdit: boolean = false;
  transducer: TransducerDB = null;
  channel: ChannelSerializerWithPopulate = null;
  transducerList: TransducerDB[] = [];
  sensitivity2: any;

  add(): void {
    const {name, signal_type, id} = this.transducer;
    if (this.tabName !== "Input") {
      // Config Output
      const params = {
        ChannelType: "Output",
        ChannelIndex: parseInt(this.data.channel_num) - 1,
        TransducerName: name,
        Base64Sensitivity: this.encode(this.sensitivity2),
        SensitivityUnit: signal_type, //btoa(String(signal_type)),
        // Base64Coefficients: Base64Coefficients,  //CoefficientsFile
      };
      const command = "config;" + JSON.stringify(params);
      this.api.getPALSendCommand({id: this.sn, cmd: command}).then(() => {
        this.api.editChannel({transducerdb: id}, this.data.id).then(() => {
          this.refresh();
          this.clean();
        });
      });
    } else {
      // Config Input
      const params = {
        ChannelType: "Input",
        ChannelIndex: parseInt(this.data.channel_num) - 1,
        TransducerName: name,
        Base64Sensitivity: this.encode(this.sensitivity2),
        SensitivityUnit: signal_type, //btoa(String(signal_type)),
        IsPhantomPowerOn: true,
      };
      const command = "config;" + JSON.stringify(params);
      this.api.getPALSendCommand({id: this.sn, cmd: command}).then(() => {
        this.api.editChannel({transducerdb: id}, this.data.id).then(() => {
          this.refresh();
          this.clean();
        });
      });
    }
    // TODO: Fix channel
    // const {calibration_date, name, category} = this.transducer;
    // const content = `Category: ${category} / Name: ${name} / Calibration Date: ${formatDate(
    //   calibration_date,
    //   DateFormat.YMD,
    // )}`;
    // const formData = {lab: this.lab, content};
    // this.api.addTransducerDBSetting(formData).then(() => {
    //   this.refresh();
    //   this.clean();
    // });
  }

  encode(float64Number): string {
    const keyString = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789+/=";

    const arrayBuffer = new ArrayBuffer(8);
    const floatView = new Float64Array(arrayBuffer);
    const bytes = new Uint8Array(arrayBuffer);
    const byteLength = bytes.byteLength;
    const byteRemainder = byteLength % 3;
    const mainLength = byteLength - byteRemainder;

    floatView[0] = float64Number;

    let base64 = "";
    let a, b, c, d;
    let chunk;

    for (let i = 0; i < mainLength; i = i + 3) {
      chunk = (bytes[i] << 16) | (bytes[i + 1] << 8) | bytes[i + 2];

      a = (chunk & 16515072) >> 18;
      b = (chunk & 258048) >> 12;
      c = (chunk & 4032) >> 6;
      d = chunk & 63;

      base64 += keyString[a] + keyString[b] + keyString[c] + keyString[d];
    }

    if (byteRemainder == 1) {
      chunk = bytes[mainLength];

      a = (chunk & 252) >> 2;
      b = (chunk & 3) << 4;

      base64 += keyString[a] + keyString[b] + "==";
    } else if (byteRemainder == 2) {
      chunk = (bytes[mainLength] << 8) | bytes[mainLength + 1];

      a = (chunk & 64512) >> 10;
      b = (chunk & 1008) >> 4;
      c = (chunk & 15) << 2;

      base64 += keyString[a] + keyString[b] + keyString[c] + "=";
    }

    return base64;
  }

  clean(): void {
    this.category = "";
    this.transducer = null;
    this.dismiss();
  }

  onSelectCategory(category: string): void {
    const query = paramsToQuery({device_category: category});
    this.api.getTDBList({query}).then((res) => {
      this.transducerList = res;
    });
  }

  onSelectTransducer(transducer: TransducerDB): void {
    this.transducer = {
      category: "computing...",
      device_category: "computing...",
      manufacture: "computing...",
      model: "computing...",
      serial_num: "computing...",
      name: "computing...",
    };

    setTimeout(() => {
      this.transducer = transducer;
      this.sensitivity2 = typeof this.sensitivity == "number" ? this.sensitivity : this.transducer ? this.transducer.sensitivity : null;
    }, 1000);
  }
}
