






























































































































import AddRequirementModal from "@/components/events/AddRequirementModal.vue";
import AddResultModal from "@/components/events/AddResultModal.vue";
import AsModal from "@/mixins/AsModal";
import {Component, Emit, Mixins, Prop} from "vue-property-decorator";

@Component({
  components: {AddResultModal, AddRequirementModal},
})
export default class EventAnalysisModal extends Mixins(AsModal) {
  @Emit("refresh") refresh(): void {}
  @Prop({type: Object, default: () => null}) readonly data;
  @Prop({type: Array, default: () => []}) readonly measurementsource;

  mounted() {
    if (this.data) {
	  console.log(JSON.stringify(this.data));
	  this.eventData = this._.pick(this.data, ["analysisName", "results", "requirementData", "expression"]);
	  console.log(JSON.stringify(this.eventData.expression));
    }
  }

  analysisName = [
    {
      title: "Level vs Time",
      category: ["All", "Time", "Level"],
      infoUrl: "https://pmtvan9j3i.execute-api.ap-southeast-1.amazonaws.com/Prod/info",
    },
    {
      title: "Fourier Spectrum",
      category: ["All", "Spectrum", "Level"],
      infoUrl: "https://27pp4bbmxf.execute-api.ap-southeast-1.amazonaws.com/Prod/info",
    },
    {
      title: "Octave Spectrum",
      category: ["All", "Spectrum", "Level"],
      infoUrl: "https://hmh240r9y6.execute-api.ap-southeast-1.amazonaws.com/Prod/info",
    },
    {
      title: "Butterworth Filter",
      category: ["All", "Filter"],
      infoUrl: "https://pmtvan9j3i.execute-api.ap-southeast-1.amazonaws.com/Prod/info",
    },
    {
      title: "Chebyshev 1 Filter",
      category: ["All", "Filter"],
      infoUrl: "https://pmtvan9j3i.execute-api.ap-southeast-1.amazonaws.com/Prod/info",
    },
    {
      title: "Chebyshev 2 Filter",
      category: ["All", "Filter"],
      infoUrl: "https://pmtvan9j3i.execute-api.ap-southeast-1.amazonaws.com/Prod/info",
    },
  ];

  eventData = {
    analysisName: "",
    results: [],
    requirementData: [],
    expression: {
    //   xMin: "",
    //   xMax: "",
      xScale: "",
    //   yMin: "",
    //   yMax: "",
	  yScale: "",
	  xRange: [],
	  yRange: [],
    },
  };

  eventFields = [
    {
      title: "Analysis Category",
      type: "select",
      options: [{title: "All"}, {title: "Time"}, {title: "Level"}, {title: "Spectrum"}, {title: "Filter"}],
      default: "All",
      placeholder: "",
      value: "All",
    },
    {
      title: "Analysis Name",
      type: "select",
      options: [], // analysisNameFilterByCategory
      default: "",
      placeholder: "",
      value: "",
    },
  ];

  eventInfo = {};

  displayAddResultModal: boolean = false;
  displayAddRequirementModal: boolean = false;

  deleteResultData(index: number): void {
    this.eventData.results.splice(index, 1);
  }

  deleteRequirementData(index: number): void {
    this.eventData.requirementData.splice(index, 1);
  }

  addResultData(data: object) {
    if (data) {
      this.eventData.results.push({...data} as any);
    }
  }

  addRequirementData(data: object) {
    if (data) {
      this.eventData.requirementData.push({...data} as any);
    }
  }

  get analysisNameFilterByCategory(): any[] {
    return this.analysisName.filter((option) => {
      return option.category.some((category) => {
        return category == this.eventFields[0].value;
      });
    });
  }

  getEventInfo(title: string): void {
    switch (title) {
      case "Level vs Time": {
        this.api.getLevelTimeApi(0, {}).then((res) => {
          this.eventInfo = res;
        });
        break;
      }
      case "Fourier Spectrum": {
        this.api.getFourierSpectrumApi(0, {}).then((res) => {
          this.eventInfo = res;
        });
        break;
      }
      case "Octave Spectrum": {
        this.api.getOctaveSpectrumApi(0, {}).then((res) => {
          this.eventInfo = res;
        });
        break;
      }
      default: {
        this.eventInfo = {};
        break;
      }
    }
  }
}
