export enum PERMISSION_MUTATION {
  UPDATE_PERMISSION = "UPDATE_PERMISSION",
  CLEAR = "CLEAR",
}

export enum PERMISSION_ACTION {
  UPDATE_PERMISSION = "permission/UPDATE_PERMISSION",
  CLEAR = "permission/CLEAR",
}

export interface Permission {
  status: number;
  token: string;
  username: string;
  id: number;
  email: string;
}

export interface PermissionMutations {
  UPDATE_PERMISSION(permission: Permission): void;

  CLEAR(): void;
}

export interface PermissionActions {
  UPDATE_PERMISSION(permission: Permission): void;

  CLEAR(): void;
}
