import {SequenceJSON, SequenceTask} from "@/types/SequenceJSON";

export function buildImportSequence(content: string): string {
  const parsedContent: SequenceJSON = JSON.parse(content);
  if (checkMeasurement(parsedContent)) {
    parsedContent.tasks = buildMeasurement(parsedContent);
    parsedContent.tasks = buildPause(parsedContent);
  }

  return JSON.stringify(parsedContent);
}

function checkMeasurement(parsedContent: SequenceJSON): boolean {
  return parsedContent.tasks.some((task) =>
    task.events.some((event: any) => {
      return event.type === "Measurement" && event.sources;
    }),
  );
}

function buildMeasurement(parsedContent: SequenceJSON): SequenceTask[] {
  return parsedContent.tasks.map((task) => {
    return {
      ...task,
      events: task.events.map((event: any) => {
        if (event.type === "Measurement") {
          const playbackChannelData = event.sources
            .filter((source) => source.type === "output")
            .map((source: any) => {
              return {
                adjustment: source.levelAdjustment,
                channelIndex: source.channel,
                delayTime: 0,
                name: source.title,
                sourceFileChannel: source.channel,
                sourceFileName: source["fileName"],
                sourceFileLength: 0,
                skipTime: 0,
                takeLength: 0,
              };
            });
          const recordingData = event.sources
            .filter((source) => source.type === "input")
            .map((source: any) => {
              return {
                channelIndex: source.channel,
                name: source.title,
              };
            });

          return {
            id: event.id,
            name: event.name,
            type: event.type,
            eventLength: event.duration,
            playbackChannelData,
            recordingData,
          };
        } else {
          return event;
        }
      }),
    };
  });
}

function buildPause(parsedContent: SequenceJSON): SequenceTask[] {
  return parsedContent.tasks.map((task) => {
    return {
      ...task,
      events: task.events.map((event: any) => {
        if (event.type === "Pause") {
          return {
            id: event.id,
            name: event.name,
            type: event.type,
            pauseMode: event.mode,
            eventLength: event.duration,
          };
        } else {
          return event;
        }
      }),
    };
  });
}
