import {Loading} from "element-ui";

export function asPromised<T>(promise: Promise<T>, defaultData: T, withLoading: boolean = false): Promise<T> {
  const loading = withLoading && Loading.service({target: ".tabs", body: true});
  return promise
    .catch((err) => {
      console.warn(err);
      const errorCode = err.response && err.response.data ? err.response.data.code : err.code;
      const errorMessage = err.response && err.response.data ? err.response.data : err.message;
      return Object.assign({}, defaultData, {errorMessage, code: errorCode});
    })
    .finally(() => {
      withLoading && loading && loading.close();
    });
}
