






















import AsModal from "@/mixins/AsModal";
import {Component, Emit, Mixins, Prop} from "vue-property-decorator";

@Component
export default class AddRecordingChannelModal extends Mixins(AsModal) {
  @Emit("refresh") refresh(data: object): void {}
  @Prop({type: Array, default: () => []}) readonly indexes;

  clean(): void {
    this.resetForm();
    this.dismiss();
  }

  resetForm(): void {
    this.$nextTick(() => {
      const ref: any = this.$refs["recordingChannelRuleFormRef"];
      ref.resetFields();
    });
    this.recordingChannelRuleForm = {
      channelIndex: null,
      name: "",
    };
  }

  addRecording(): void {
    const ref: any = this.$refs["recordingChannelRuleFormRef"];
    ref.validate((valid) => {
      if (valid) {
        this.refresh(this.recordingChannelRuleForm);
        this.clean();
      } else {
        return false;
      }
    });
  }

  recordingChannelRuleForm = {
    channelIndex: null,
    name: "",
  };
  recordingChannelRule = {
    name: [{required: true, message: "Channel name is required.", trigger: "change"}],
  };
}
