


























































import CalibrationEventModal from "@/components/events/CalibrationEventModal.vue";
import ActuatorTable from "@/components/transcuder-db-device/ActuatorTable.vue";
import AddTransducerModal from "@/components/transcuder-db-device/AddTransducerModal.vue";
import EditTransducerModal from "@/components/transcuder-db-device/EditTransducerModal.vue";
import SensorTable from "@/components/transcuder-db-device/SensorTable.vue";
import {DateFormat} from "@/core/constants/DateFormat";
import {formatDate} from "@/core/utils/formatDate";
import {TransducerDB} from "@/types/ApiTypes";
import {Component, Vue} from "vue-property-decorator";

@Component({
  components: {EditTransducerModal, AddTransducerModal, CalibrationEventModal},
})
export default class TransducerDbDeviceView extends Vue {
  data: TransducerDB[] = [];
  originalData: TransducerDB[] = [];
  tabs = [{name: "Actuator", component: ActuatorTable}, {name: "Sensor", component: SensorTable}];
  displayAddModal: boolean = false;
  displayEditModal: boolean = false;
  displayCalibrationEventModal: boolean = false;
  editId: number = null;

  searchString: string = "";
  activeTab: string = this.tabs[0].name;

  mounted(): void {
    this.getSpeakerData();
  }

  getData(): void {
    if (this.activeTab === "Sensor") {
      this.getSensorData();
    } else {
      this.getSpeakerData();
    }
  }

  getSpeakerData(): void {
    this.api.getTDBList({query: "device_category:Actuator", sort_by: "calibration_date:1"}).then((res) => {
      this.originalData = res;
      this.data = this.originalData;
    });
  }

  getSensorData(): void {
    this.api.getTDBList({query: "device_category:Sensor", sort_by: "calibration_date:1"}).then((res) => {
      this.originalData = res;
      this.data = this.originalData;
    });
  }

  displayContent(transducer: TransducerDB): string {
    const {calibration_date, name, category} = transducer;
    return `Category: ${category} / Name: ${name} / Calibration Date: ${formatDate(calibration_date, DateFormat.YMD)}`;
  }

  editTransducer(id: number): void {
    this.editId = id;
    this.displayEditModal = true;
  }

  deleteTransducer(id: number): void {
    this.api.deleteTDB(id).then(() => this.getData());
  }

  closeEdit(): void {
    this.displayEditModal = false;
    this.editId = null;
  }

  inputString(str: string): void {
    if (str === "") {
      this.data = this.originalData;
    } else {
      this.data = this.originalData.filter((it: TransducerDB) => {
        return Object.values(it).some((val) => {
          if (typeof val === "string") {
            return val.toUpperCase().includes(str.toUpperCase());
          }
          return false;
        });
      });
    }
  }
}
