
























import {DeviceManagement, LabSerializerWithPopulate} from "@/types/ApiTypes";
import {Component, Emit, Mixins, Prop, Watch} from "vue-property-decorator";
import AsModal from "../../mixins/AsModal";

@Component
export default class EditLabModal extends Mixins(AsModal) {
  @Emit("refresh") refresh(): void {}

  @Prop(Object) readonly lab!: LabSerializerWithPopulate;

  device: number = null;
  deviceList: DeviceManagement[] = [];

  @Watch("visible")
  onVisibleChange(val: boolean): void {
    if (val) {
      this.device = this.lab.device_manager.id;
      const exception = true;
      this.api.getDeviceList({fields: "id,name", exception}).then((res) => {
        this.deviceList = res;
      });
    }
  }

  submit(): void {
    const lab = {...this._.pick(this.lab, ["description", "name"]), device_manager: this.device};
    this.api.putLab(lab, this.lab.id).then(() => {
      this.clean();
    });
  }

  clean(): void {
    this.refresh();
    this.deviceList = [];
    this.dismiss();
  }
}
