













import {Component, Emit, Mixins} from "vue-property-decorator";
import AsModal from "../../mixins/AsModal";

@Component
export default class GetInvoiceModal extends Mixins(AsModal) {
  @Emit("refresh") refresh(): void {}
  searchDate: Date = new Date();

  getBilling(): void {
    this.api.postBillingMail({mail: "user@gmail.com", admin_mail: "admin@gmail.com", year: this.searchDate.getFullYear(), month: this.searchDate.getMonth()}).then((res) => {
      this.$message({message: res.msg, type: "success"});
      this.dismiss();
    });
  }
}
