
























import {initialLab} from "@/core/constants/api/initialLab";
import {DeviceManagement, Lab} from "@/types/ApiTypes";
import {Form} from "element-ui";
import {Component, Emit, Mixins, Prop, Watch} from "vue-property-decorator";
import AsModal from "../../mixins/AsModal";

@Component
export default class AddLabModal extends Mixins(AsModal) {
  @Prop({type: Object, default: () => initialLab()}) readonly data!: Lab;

  @Emit("refresh") refresh(): void {}

  @Emit()
  addLab(id: number) {
    return id;
  }

  formData: Lab = {...initialLab()};
  deviceList: DeviceManagement[] = [];

  @Watch("visible")
  onVisibleChange(val: boolean): void {
    if (val) {
      if (this.data !== null) {
        this.formData = this.data;
      }
      const exception = true;
      this.api.getDeviceList({fields: "id,name", exception}).then((res) => {
        this.deviceList = res;
      });
    }
  }

  submit(): void {
    (this.$refs.form as Form).validate((valid) => {
      if (valid) {
        this.api.addLab({...this.formData}).then((res) => {
          this.addLab(res.id);
          this.clean();
        });
      } else {
        return false;
      }
    });
  }

  clean(): void {
    (this.$refs.form as Form).resetFields();
    this.formData = {...initialLab()};
    this.dismiss();
  }
  rules = {
    name: [{required: true, message: "Lab Name is required.", trigger: "change"}],
    device_manager: [{required: true, message: "Hardware is required.", trigger: "change"}],
  };
}
