



































import ChangePasswordModal from "@/components/ChangePasswordModal.vue";
import ProfileModal from "@/components/ProfileModal.vue";
import {AppContext} from "@/core/api/AppContext";
import {TABS_ACTION, TabsActions} from "@/types/store/TabStore";
import {Component, Emit, Vue} from "vue-property-decorator";
import {Action, Getter} from "vuex-class";
import {PERMISSION_ACTION, PermissionActions} from "@/types/store/PermissionStore";

@Component({
  components: {ProfileModal, ChangePasswordModal},
})
export default class SiteHeader extends Vue {
  @Getter("permission/status") readonly status!: number;
  @Getter("permission/username") readonly username!: string;
  @Getter("permission/email") readonly email!: string;
  @Emit("doActive") doActive(): void {};
  displayChangePasswordModal: boolean = false;
  displayProfileModal: boolean = false;
  isActive: boolean = false;

  @Action(TABS_ACTION.UPDATE_CURRENT_TAB)
  updateTab!: TabsActions["UPDATE_CURRENT_TAB"];

  @Action(PERMISSION_ACTION.CLEAR)
  clearToken!: PermissionActions["CLEAR"];

  redirect(name: string): void {
    this.clearToken();
    AppContext.buildApiExecutor();
    this.$router.push({name});
  }

  goToTab(tabName: string): void {
    this.$router.push({query: {tab: tabName}});
    this.updateTab(tabName);
  }

  get dropdownItems(): any[] {
    return this.status === 2
      ? []
      : this.status === 1
      ? [
          {
            label: "My Profile",
            action: () => {
              this.displayProfileModal = true;
            },
          },
          {
            label: "Change password",
            action: () => {
              this.displayChangePasswordModal = true;
            },
          },
        ]
      : [
          {
            label: "Plan & Setting",
            action: () => {
              this.goToTab("PlanSetting");
            },
          },
          {
            label: "Change password",
            action: () => {
              this.displayChangePasswordModal = true;
            },
          },
        ];
  }

  toggle(): void {
	this.isActive = !this.isActive;
    this.$emit("doActive", this.isActive);
  }
}
