
























import AsModal from "@/mixins/AsModal";
import {Component, Emit, Mixins, Prop} from "vue-property-decorator";
import {formatDate} from "@/core/utils/formatDate";

@Component
export default class UploadAudioModal extends Mixins(AsModal) {
  @Emit("refresh") refresh(): void {}

  @Prop({type: Array, default: () => []}) readonly audio;
  clean(): void {
    this.dismiss();
  }

  audio_url: string = "";
  upload(options: {file: File}): Promise<void> {
    this.audio_url = "uploading...";
    return this.api
      .upload(options.file, "path")
      .then((res) => {
        this.audio_url = res;
        const date = formatDate(Date.now(), "YYYYMMDDTHHmmss");
        this.onChange({name: options.file.name, url: res, date});
        this.audio.push({name: options.file.name, url: res, date});
      })
      .catch(console.error);
  }

  @Emit("update:audio")
  onChange(audio): any[] {
    return [...this.audio, audio];
  }
}
