























import {DateFormat} from "@/core/constants/DateFormat";
import {formatDate} from "@/core/utils/formatDate";
import {TransducerDB} from "@/types/ApiTypes";
import {Component, Prop, Vue} from "vue-property-decorator";

@Component
export default class ActuatorTable extends Vue {
  @Prop({type: Array, default: () => []}) readonly data!: TransducerDB[];

  displayContent(transducer: TransducerDB): string {
    const {calibration_date, name, category} = transducer;
    return `Category: ${category} / Name: ${name} / Calibration Date: ${formatDate(calibration_date, DateFormat.YMD)}`;
  }
}
