



















import AsModal from "@/mixins/AsModal";
import {ChangePassword} from "@/types/ApiTypes";
import {PERMISSION_ACTION, PermissionActions} from "@/types/store/PermissionStore";
import {Component, Emit, Mixins, Prop, Watch} from "vue-property-decorator";
import {Action, Getter} from "vuex-class";

@Component
export default class ProfileModal extends Mixins(AsModal) {
  @Action(PERMISSION_ACTION.UPDATE_PERMISSION)
  updatePermission!: PermissionActions["UPDATE_PERMISSION"];

  @Emit("refresh") refresh(): void {}
  @Prop(String) readonly email!: string;
  @Prop(String) readonly name!: string;
  @Getter("permission/id") readonly id!: number;
  @Getter("permission/token") readonly token!: string;

  editName: string = "";
  @Watch("visible")
  onVisibleChange(val: boolean): void {
    if (val) {
      this.editName = this.name;
    } else {
      this.editName = "";
    }
  }
  clean(): void {
    this.dismiss();
  }

  change(): void {
    this.api.patchAccountName(this.editName, this.id).then((res) => {
      const {id, status, name, account} = res;
      this.updatePermission({id, status, username: name, email: account, token: this.token});
      this.clean();
    });
  }
}
