
































import {initialProjectManagement} from "@/core/constants/api/initialProjectManagement";
import {paramsToQuery} from "@/core/utils/paramsToQuery";
import AsModal from "@/mixins/AsModal";
import {GlobalTag, ProjectManager} from "@/types/ApiTypes";
import {Component, Emit, Mixins, Prop, Watch} from "vue-property-decorator";
import {Action, Getter} from "vuex-class";
import {TABS_ACTION, TabsActions} from "@/types/store/TabStore";

@Component
export default class AddProjectModal extends Mixins(AsModal) {
  @Getter("permission/id") readonly userId!: number;
  @Prop(Number) readonly id!: number;
  @Emit("refresh") refresh(): void {}
  @Action(TABS_ACTION.UPDATE_CURRENT_TAB)
  updateTab!: TabsActions["UPDATE_CURRENT_TAB"];

  formData: ProjectManager = {...initialProjectManagement()};
  sequenceOptions = [{label: "Global Sequence", options: []}, {label: "Your Sequence", options: []}];
  originalSequenceOptions = [{label: "Global Sequence", options: []}, {label: "Your Sequence", options: []}];
  selectedTag: number = -1;
  tagList: GlobalTag[] = [];

  @Watch("visible")
  onVisibleChange(val: boolean): void {
    if (val) {
      this.formData.lab = this.id;
      this.api.getTagList(null).then((tagList) => {
        this.tagList = tagList;
      });
      this.api.getSmList({query: paramsToQuery({is_customize: "False"})}).then((res) => {
        this.originalSequenceOptions[0].options = res;
        this.sequenceOptions[0].options = this.originalSequenceOptions[0].options;
      });
      this.api.getSmList({query: paramsToQuery({user: this.userId, is_customize: "True"})}).then((res) => {
        this.originalSequenceOptions[1].options = res;
        this.sequenceOptions[1].options = this.originalSequenceOptions[1].options;
      });
    } else {
      this.clean();
    }
  }

  goToTab(id: number): void {
    this.$router.push({query: {tab: "ProjectWorker", id: String(id)}});
    this.updateTab("ProjectWorker");
  }

  save() {
    this.api.addProjectManagement(this.formData).then((res) => {
      this.refresh();
      this.clean();
      this.goToTab(res.id);
    });
  }

  clean(): void {
    this.selectedTag = null;
    this.sequenceOptions = [{label: "Global Sequence", options: []}, {label: "Your Sequence", options: []}];
    this.originalSequenceOptions = [{label: "Global Sequence", options: []}, {label: "Your Sequence", options: []}];
    this.formData = {...initialProjectManagement()};
    this.dismiss();
  }

  onSelectTag(tagId: number): void {
    if (tagId === -1) {
      this.sequenceOptions[0].options = this.originalSequenceOptions[0].options;
      this.sequenceOptions[1].options = this.originalSequenceOptions[1].options;
    } else {
      this.sequenceOptions[0].options = this.originalSequenceOptions[0].options.filter((option) => {
        return option.tags.split(",").includes(String(tagId));
      });
      this.sequenceOptions[1].options = this.originalSequenceOptions[1].options.filter((option) => {
        return option.tags.split(",").includes(String(tagId));
      });
    }
  }
}
