





































































































import {Component, Vue} from "vue-property-decorator";
import {Getter} from "vuex-class";
import {Account, LabSerializerWithPopulate} from "@/types/ApiTypes";

@Component
export default class PlanSettingView extends Vue {
  @Getter("permission/id") readonly id!: number;

  mounted(): void {
    this.getData();
  }

  account: Account = null;
  billingStatus = [];
  labList: LabSerializerWithPopulate[] = [];

  getData(): void {
    this.api.getUserAccount(this.id).then((res) => {
      this.account = res;
      this.api.getLabList({ids: res.labs.map((lab) => lab.id).join(",")}).then((labs) => {
        this.labList = labs;
      });
      this.billingStatus = [{...res, labs: res.labs.map((lab) => lab.name).join(",")}];
    });
  }
}
