
















































import {Plotly} from 'vue-plotly';
import AsModal from "@/mixins/AsModal";
import {Component, Emit, Mixins, Prop, Watch} from "vue-property-decorator";
import {Getter} from "vuex-class";
import {ChannelSerializerWithPopulate} from "@/types/ApiTypes";

@Component({
  components: { Plotly }
})
export default class CalibrationEventModal extends Mixins(AsModal) {
  @Getter("permission/email") readonly email!: string;
  @Emit("refresh") refresh(): void {}
  @Emit("doCalibrate") doCalibrate(): void {}
  @Prop({type: String, default: () => ""}) readonly tabName!: string;
  @Prop({type: String, default: () => null}) readonly sn!: string;
  @Prop({type: Boolean, default: () => false}) readonly equalization!: boolean;
  @Prop({type: Number, default: () => 0}) readonly ChannelIndex!: number;
  @Prop({type: Object, default: () => null}) readonly config!: ChannelSerializerWithPopulate;
  excuteCallback: string = "";
  data = {
    SensitivityFrequency: 1000,
    SourceLevel: 0.5,
    IsPhantomPowerOn: true,
    EqStartFrequency: 100,
    EqStopFrequency: 10000
  };
  calibrateCallback: any = {};
  chartdata = [];
  show = true;
  plot_data = [];
  plot_layout = {};

  @Watch("visible")
  onVisibleChange(val: boolean): void {
    if (val) {
	  this.calibrateCallback = {};
      this.data = {
        SensitivityFrequency: 1000,
        SourceLevel: 0.5,
        IsPhantomPowerOn: true,
        EqStartFrequency: 100,
        EqStopFrequency: 10000
      };
      this.chartdata = [];
    }
  }  
  clean(): void {
    this.dismiss();
  }

  decode(): any {
    if( this.calibrateCallback["Base64Sensitivity"] ) {
      const keyString = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789+/=";

      let base64String = this.calibrateCallback.Base64Sensitivity;
      const removePaddingChars = input => {
        const lkey = keyString.indexOf(input.charAt(input.length - 1));
        if (lkey == 64) {
        return input.substring(0, input.length - 1);
        }
        return input;
      };

      base64String = removePaddingChars(base64String);
      base64String = removePaddingChars(base64String);

      const numBytes = base64String.length / 4 * 3;

      let chr1, chr2, chr3;
      let enc1, enc2, enc3, enc4;
      let j = 0;

      const uarray = new Uint8Array(numBytes);

      base64String = base64String.replace(/[^A-Za-z0-9\+\/\=]/g, "");

      for (let i = 0; i < numBytes; i += 3) {
        enc1 = keyString.indexOf(base64String.charAt(j++));
        enc2 = keyString.indexOf(base64String.charAt(j++));
        enc3 = keyString.indexOf(base64String.charAt(j++));
        enc4 = keyString.indexOf(base64String.charAt(j++));

        chr1 = (enc1 << 2) | (enc2 >> 4);
        chr2 = ((enc2 & 15) << 4) | (enc3 >> 2);
        chr3 = ((enc3 & 3) << 6) | enc4;

        uarray[i] = chr1;
        if (enc3 != 64) uarray[i + 1] = chr2;
        if (enc4 != 64) uarray[i + 2] = chr3;
      }

      this.calibrateCallback["decode"] = new DataView(uarray.buffer).getFloat64(0, true);
	  // return new DataView(uarray.buffer).getFloat64(0, true);
	  return JSON.stringify(this.calibrateCallback);
    } else {
      return this.config ? this.config.transducerdb.sensitivity : "";
    }
    // return this.calibrateCallback.Base64Sensitivity ? atob(this.calibrateCallback.Base64Sensitivity) : "";
  }

  start(): void {
    this.show = false;
    let params = {};
    this.data.SensitivityFrequency = typeof this.data.SensitivityFrequency != "number" ? parseFloat(this.data.SensitivityFrequency) : this.data.SensitivityFrequency;
    this.data.SourceLevel = typeof this.data.SourceLevel != "number" ? parseFloat(this.data.SourceLevel) : this.data.SourceLevel;
    this.data.EqStartFrequency = typeof this.data.EqStartFrequency != "number" ? parseFloat(this.data.EqStartFrequency) : this.data.EqStartFrequency;
    this.data.EqStopFrequency = typeof this.data.EqStopFrequency != "number" ? parseFloat(this.data.EqStopFrequency) : this.data.EqStopFrequency;
    if (this.tabName == "Input") {
      params = {
        InputChannel: this.ChannelIndex,
        SensitivityFrequency: this.data.SensitivityFrequency,
        SourceLevel: this.data.SourceLevel,
        IsPhantomPowerOn: this.data.IsPhantomPowerOn,
      }
    } else if (!this.equalization) {
      params = {
        InputChannel: this.ChannelIndex,
        OutputChannel: this.ChannelIndex,
        SensitivityFrequency: this.data.SensitivityFrequency,
        SourceLevel: this.data.SourceLevel,
      }
    } else {
      params = {
        InputChannel: this.ChannelIndex,
        OutputChannel: this.ChannelIndex,
        SensitivityFrequency: this.data.SensitivityFrequency,
        SourceLevel: this.data.SourceLevel,
        EqStartFrequency: this.data.EqStartFrequency,
        EqStopFrequency: this.data.EqStopFrequency
      }
    }
    // Config Output
    const command = "calibrate;" + JSON.stringify(params);
    this.api.getPALSendCommandHandle({id: this.sn, cmd: command}).then((res) => {
      this.show = true;
      this.calibrateCallback = JSON.parse(res["cmd"]);
      if (!this.calibrateCallback) {
        this.$message.error('Time out');
      } else if (this.calibrateCallback.Status == "Done") {
        // this.$message.success('Done');
        if (this.equalization) {
          let file = this.calibrateCallback.RecordedFile.split("temp\\")[1];
          this.api.s3upload({id: this.sn, src: `temp//${file}`, dst: `s3://${this.email}/${file}`}).then((res) => {
            if ( this.equalization ) {
              this.api.doExecuteApi({
                inputs: [
                    [
                        {"name": "Bucket Name", "value": "pal.data"},
                        //   {"name": "Object Key", "value": `${this.email}/${file}`}
                        {"name": "Object Key", "value": `${this.email}/1000Hz.wav`}
                    ]
                ]              
              }).then((res) => {
                this.excuteCallback = JSON.stringify(res);
                this.plotly(res);
                // this.chartdata = res["samples"];
                this.$message.success('Upload Done');
              });
            } else {
                this.$message.success('Upload Done');
            }
          });
        }
      } else {
        // this.$message.error('parameter error');
        this.$message.error(JSON.parse(res["cmd"]).ErrorMessage);
      }
    });      
  }

  stop(): void {
    this.show = true;
    this.api.getPALSendCommandHandle({id: this.sn, cmd: "stop"}).then(() => {
      this.$message.success('Stop');
    });      
  }

  save(): void {
    this.$emit("doCalibrate", this.calibrateCallback);
    this.dismiss();
  }

  plotly(result: any): void {
    const { info, samples, axes } = result;
    const resultUnit = info.isDbValue ? info.dbUnit : info.unit;

    if (result.info.dimension === 1) {
      const data = [{
        x: this.createAxisSamples(axes[0]),
        y: samples,
        name: `${result.info.name}: ${Math.round(result.singleValue * 100) / 100} [${resultUnit}]`
      }];

      const layout = {
        title: info.moduleName,
        xaxis: {
          title: `${axes[0].title} [${axes[0].unit}]`,
          linecolor: "black",
          linewidth: 1,
          mirror: true,
          zeroline: false
        },
        yaxis: {
          title: `${info.title} [${resultUnit}]`,
          linecolor: "black",
          linewidth: 1,
          mirror: true,
          zeroline: false
        },
        showlegend: true,
        legend: {
          x: 0.05,
          y: 0.95,
          bgcolor: "#FFFFFF",
          bordercolor: "#000000",
          borderwidth: 1,
          traceorder: "reversed",
          font: {
            size: 12
          }
        }
      };

      this.plot_data = data;
      this.plot_layout = layout;
    }

    if (result.info.dimension === 2) {
      const data = [{
        x: this.createAxisSamples(axes[0]),
        y: this.createAxisSamples(axes[1]),
        z: this.createSampleMatrix(samples, axes[0].length, axes[1].length),
        type: "heatmap",
        name: result.info.name
      }];

      const layout = {
        title: info.moduleName,
        xaxis: {
          title: `${axes[0].title} [${axes[0].unit}]`,
          autorange: true,
          linecolor: "black",
          linewidth: 1,
          mirror: true,
          zeroline: false
        },
        yaxis: {
          title: `${axes[1].title} [${axes[1].unit}]`,
          linecolor: "black",
          linewidth: 1,
          mirror: true,
          zeroline: false
        },
        showlegend: true,
        legend: {
          traceorder: "reversed",
          font: {
            size: 12
          }
        }
      }; 

      this.plot_data = data;
      this.plot_layout = layout;
    }      
  }
  
  createAxisSamples(axis): any {
    if (axis.isLinearSpaced) {
      const { startValue, deltaValue, length } = axis;
      const data = [];
      for (let i = 0; i < length; i++) {
        data.push(startValue + i * deltaValue);
      }
      return data;
    }

    return axis.values;
  }

  createSampleMatrix(samples, xLength, yLength): any {
    const matrix = [];

    for (let i = 0; i < yLength; i++) {
      const row = [];
      for (let j = 0; j < xLength; j++) {
        row.push(samples[i + j * yLength]);
      }
      matrix.push(row);
    }

    return matrix;
  }  

  mockResultData = [{name: "Head Front"}, {name: "Head  Right"}, {name: "Head  Left"}];
  mockRequirementData = [{name: "Manual Point"}, {name: "Value Limit"}];
  ei: any;
}
