

















import NavMenu from "@/layouts/NavMenu.vue";
import SiteHeader from "@/layouts/SiteHeader.vue";
import {Tab} from "@/types/store/TabStore";
import DefaultView from "@/views/DefaultView.vue";
import {Component as Comp} from "vue";
import {Component, Vue} from "vue-property-decorator";
import {Getter} from "vuex-class";

@Component({
  components: {NavMenu, SiteHeader},
})
export default class App extends Vue {
  @Getter("tabs/all")
  readonly tabs!: Tab[];

  @Getter("tabs/currentTab")
  readonly currentTab!: Tab;

  isActive: boolean = false;

  get activeTab(): Comp {
    return this.hasTab(this.currentTab) ? this.currentTab.tab : DefaultView;
  }

  hasTab(tab: Tab): boolean {
    return tab && this.tabs.some((it) => it.name === tab.name);
  }

  doActive(active: boolean): void {
	this.isActive = active;
  }
}
